<template>
	<div :id="id" class="butterfly-animation-wrapper" :class="[position]">
		<lottie :id="id" :options="this.defaultOptions" v-on:animCreated="handleAnimation" v-on:animCompleted="animationCompleted" ref="lottie" />

		<!-- 
		width and height not set on element, so responsive size can be acheived with css
		:height="parseInt(height)" :width="parseInt(width)"
		-->
	</div>
</template>

<script>

import LottieBase from './LottieBase';
import butterflyAnimationData from '../../../static/animation-json/butterfly-small-mask.json';

export default {
	name: 'ButterflyAnim',
	components: {
		lottie: LottieBase, // <lottie :options="defaultOptions" :height="400" :width="400" v-on:animCreated="handleAnimation"/>
	},
	props: {
		id: {
			type: String,
			default: null,
		},
		position: {
			type: String,
			default: 'top-right',
		},
		autoplay: Boolean,
		width: String,
		height: String,
		sequenceDelay: {
			type: Number,
			default: 3000, // 3 seconds - should, at least, be more than the intro duration ([0, 70])
		},
	},
	data(){
		return {
			defaultOptions: {
				animationData: butterflyAnimationData,
				loop: false,
				// loop: 4,
				autoplay: false,
			},
			animationSpeed: 1,
			direction: 1, // forward animation
			totalFrames: 0, // 122 (null based = 122 - 1 = 121 is the last frame)
			outroStartFrame: 70, // from where it will reverse the animation (70 to 0)
			sequenceIsPlaying: false,
			sequenceInterval: null,
			// sequenceDelay: 3000, // 3 seconds
		};
	},
	mounted(){
		if(this.$props.autoplay === true){
			this.anim.play();
		}
		// console.log('butterfly totalFrames: ', this.totalFrames);
	},

	methods: {
		handleAnimation(anim){ // callback of lottie animCreated
			this.anim = anim;
			this.anim.setDirection(this.direction); // forward animation to start with(might not really be necessary)
			this.totalFrames = this.anim.getDuration(true) - 1; // If true, returns duration in frames, if false, in seconds.
		},
		stop(){
			this.anim.stop();
		},
		play(){
			this.$refs.lottie.show();
			this.anim.play();

			// UNCOMMENTED PERFORMANCE TEST
			//this.playSequence();

			// var curFrame = this.anim.currentRawFrame;
			// console.log('curFrame: ', curFrame);
			// this.anim.addEventListener('enterFrame', function(e){
			// 	console.log('e: ', this.anim.currentFrame);
			// 	if(this.anim.currentRawFrame > this.outroStartFrame && this.sequenceIsPlaying === false){
			// 		this.anim.removeEventListener('enterFrame');
			// 		this.sequenceIsPlaying = true;
			// 		this.playSequence();
			// 	}
			// }.bind(this));
		},
		playSequence(){
			this.sequenceIsPlaying = true;
			this.anim.playSegments([[0,70], [70, 92]], true);
			
			this.sequenceInterval = setInterval(() => {
				this.anim.playSegments([70, 92], true);
			}, this.sequenceDelay);
		},
		playOutro(){
			clearInterval(this.sequenceInterval);
			this.sequenceIsPlaying = false;
			this.setReverse();
			this.anim.playSegments([70, 0], true);
			// this.anim.play();
		},
		pause(){
			this.anim.pause();
		},
		setReverse(){
			this.anim.setDirection(-1); // Reverse animation
		},
		setForward(){
			this.anim.setDirection(1); // Forward animation
		},
		changeSpeed(speed){
			this.anim.setSpeed(speed ? speed : this.animationSpeed);
		},
		animationCompleted(){
			// console.log('complete from butterfly?', this.$el);
			this.$emit('animationCompleted', this.anim);
		},
		jumpToFrame(frame){
			if(frame < 0){
				frame = 0
			} else if(frame > this.totalFrames){
				frame = this.totalFrames;
			}
			this.anim.goToAndStop(frame, true); // "true" for frame input
		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
// @import '../../assets/settings';

