// .flower--bottom-right {
// 	position: absolute;
// 	z-index: 3;
// }
// .flower--top-left {
// 	top: 0;
// 	left: 0;
// 	// transform: rotate(90deg);
// }
// .flower--top-right {
// 	top: 0;
// 	right: 0;
// 	transform: rotate(90deg);
// }
// .flower--bottom-left {
// 	bottom: 0;
// 	left: 0;

// 	transform: rotate(-90deg);
// }
// .flower--bottom-right {
// 	bottom: 0;
// 	right: 0;

// 	transform: scaleX(-1) rotate(-90deg);
// }
</style>



