<template>
	<div :class="isChina === true ? 'no-pointer' : ''" class="instagram-feed-entry">
		<!-- <a :href="feed.link">
			<img :src="feed.images.standard_resolution.url">
			<div class="instagram-feed-entry__content">
				<p>Likes: {{feed.likes.count}}</p>
				<p>Comments: {{feed.comments.count}}</p>
			</div>
		</a> -->
		<a :href="isChina === true ? 'javascript:;' : tmpUrl" :target="isChina === true ? '' : '_blank'" :class="isChina === true ? '' : 'custom-cursor'">
			<img :src="tmpFeed.img">
			<div class="instagram-feed-entry__content">
				<!-- <p>Likes: {{feed.likes.count}}</p>
				<p>Comments: {{feed.comments.count}}</p> -->
			</div>
		</a>
	</div>
</template>

<script>
export default {
	name: 'InstagramFeedEntry',
	props: {
		mediaId: {
			type: String,
		},
		token: {
			type: String,
		},
		feed: {
			type: Object,
			default: () => ({
				link: '',
				images: {
					standard_resolution: {
						url: ''
					}
				},
				likes: {
					count: ''
				},
				comments: {
					count: ''
				}
			})
		},
		tmpUrl: String,
		tmpFeed: {
			type: Object,
			default: () => ({
				img: ''
			})
		},
		isChina: Boolean,
	},
	created(){
		this.tmpFeed.img = this.mediaId;

		// fetch("https://api.instagram.com/v1/media/" + this.mediaId + "/?access_token=" + this.token)
		// 	.then(function(response) {
		// 		return response.json();
		// 	})
		// 	.then(function(entryData) {
				
		// 		this.feed.link = entryData.data.link;
		// 		this.feed.images.standard_resolution.url = entryData.data.images.standard_resolution.url;
		// 		this.feed.likes.count = entryData.data.likes.count;
		// 		this.feed.comments.count = entryData.data.comments.count;
		// 	}.bind(this));


	},
	mounted(){
		// console.log('insta item - isChina: ', this.isChina);
		var img = this.$el.getElementsByTagName('img')[0];


		img.addEventListener('load', function () {
			this.$parent.imagesLoaded();
		}.bind(this));
	},
	// data: () => ({
	// }),
};
</script>

<style lang="scss" scoped>
@import '../assets/global';

.instagram-feed-entry {
	display: block;
	width: 100%;
	// max-width: 60%;
	margin-top: -40%;

	box-shadow: rem(20) rem(20) rem(80) 0 rgba(0,0,0,0.15);

	color: white;

	position: relative;

	@include mq($from: screen-md){
		width: rem(440);
		margin-top: -36%;
	}

	@include mq($from: screen-lg){
		margin-top: -30%;
	}

	&.no-pointer {
		a {
			cursor: default;
		}
	}
	
	&:not(.no-pointer){
		&:hover .instagram-feed-entry__content {
			opacity: 1;
		}
	}

	&__content {
		width: 100%;
		height: 100%;

		position: absolute;
		top: 0;
		left: 0;

		background-color: rgba(0,0,0,.35);

		color: #fff;
		text-align: center;
		font-weight: bold;
		font-size: rem(40);

		opacity: 0;

		transition: opacity .35s ease;
	}

	img {
		display: block;
		width: 100%;
	}
}
</style>



