<template>
	<section class="scene-content" :class="[className, inView ? 'active-pointer' : '']">
		<div class="bg bg--clover" ref="bg"></div>

		<CloverAnimation
			ref="clover1"
			id="clover1"
			position="flower-clover--top-right"
			width=500
			height=500
			v-on:animationCompleted="cloverAnimationCompleted"
			v-if="mq.md"
		/>

		<picture class="clover__poster-image" ref="poster">
    		<source :srcset="`${staticRoot}images/clover/Ladybird_bg_model_crop.jpg`" media="(max-width: 1279px)">
    		<source :srcset="`${staticRoot}images/clover/Ladybird_bg_model.jpg`" media="(min-width: 1280px)">
    		<img :src="`${staticRoot}images/clover/Ladybird_bg_model.jpg`" />
		</picture>


		<!-- <h2 class="scene-headline" ref="headline">Clover content headline</h2> -->
		<div class="content" ref="content">
			<span class="clover-headline" ref="headline">{{$t('clover_content_headline')}}</span>


			<a href="#" class="bubble bubble--1 bubble--product custom-cursor" ref="b1" @click.prevent="openClosedModal({sku: '197949', target: $event.target})">
				<img :src="`${staticRoot}images/products/PNGTRPNT_197949_RGB.png`" alt="" />
			</a>
			<span class="bubble bubble--2 custom-cursor" ref="b2">
				<img :src="`${staticRoot}images/clover/shutterstock_591683126_DESAT_CROP.jpg`" alt="" />
			</span>
			<span class="bubble bubble--3 custom-cursor" ref="b3">
				<img :src="`${staticRoot}images/clover/SPR19_BTL_MODEL_GARDEN_02_RGB_CROP.jpg`" alt="" />
			</span>
			<a href="#" class="bubble bubble--4 bubble--product custom-cursor" ref="b4" @click.prevent="openClosedModal({sku: '397965NAG', target: $event.target})">
				<img :src="`${staticRoot}images/products/PNGTRPNT_397965NAG_RGB.png`" alt="" />
			</a>
		</div>
	</section>
</template>

<script>
import {mapActions } from 'vuex';
import ProgressMixin from '../mixins/ProgressMixin';

import SceneHeadline from '../SceneHeadline';
// import { TimelineMax } from 'gsap'; // TimelineMax gets included via ProgressMixin
import CloverAnimation from '../animations/Clover';

export default {
	name: 'SceneContentClover',
	mixins: [ProgressMixin],
	components: {
		SceneHeadline,
		CloverAnimation,
	},
	props: {
		options: Object,
		className: String,
	},
	data() {
		return {
			mq: this.$store.state.mq,
			staticRoot: this.$store.state.staticRoot,
			products: [
				{
					scr: '/static/images/products/PNGTRPNT_187924CZ_RGB.png',
					sku: '187924CZ',
				},
				{
					scr: '/static/images/products/PNGTRPNT_187924CZ_RGB.png',
					sku: '187924CZ',
				},
				{
					scr: '/static/images/products/PNGTRPNT_187924CZ_RGB.png',
					sku: '187924CZ',
				},
				{
					scr: '/static/images/products/PNGTRPNT_187924CZ_RGB.png',
					sku: '187924CZ',
				},
			]
		};
	},
	mounted(){
		// console.log('mounted - this.options.name: ', this.options.name);
		this.$nextTick(this.setupScene);
	},
	// data() {
	//   return {
	//   };
	// },
	methods: {
		...mapActions([
			'openClosedModal',
		]),
		cloverAnimationCompleted(){
			// console.log('clover animation completed');
		},
		setupScene(){
			// const bgMain = document.querySelector('.bg--main');
			const bg = this.$refs.bg;
			const content = this.$refs.content;
			const poster = this.$refs.poster;
			const headlineTl = new TimelineMax({ paused: true });

			var staggerElements = [
				this.$refs.b1, 
				this.$refs.b2, 
				this.$refs.b3, 
				this.$refs.b4, 
			];

			var tlProgress = 0;
			var scrollDirection = 'down';
			this.introTl = new TimelineMax({paused: true, onUpdateParams: ['{self}'], onUpdate: function(introTl) {
				var currentTlProgress = introTl.progress();
				if(currentTlProgress > tlProgress){
					scrollDirection = 'down'
				} else {
					scrollDirection = 'up'
				}
				tlProgress = introTl.progress();
			}});

			this.introTl
				.set(content, {autoAlpha: 1})
				.set(bg, {autoAlpha: 0})
				// .set(bgMain, {autoAlpha: 0})
				;

			this.introTl
				.add('start')
				.to(this.$refs.headline, 0, { autoAlpha: 1 })
				.fromTo(bg, 10,
					{
						autoAlpha: 0,
						ease: Linear.easeNone,
					},
					{
						autoAlpha: 1,
						ease: Linear.easeNone,
					}
				)
				.add('poster-start')
				.to(poster, 15, 
					{
						autoAlpha: 1,
						ease: Linear.easeNone,
					},
					'start+=5'
				)
				.from(this.$refs.headline, 14, 
					{
						// y: '+=' + this.$store.state.windowHeight * .7 + 'px',
						y: '+=' + this.$store.state.windowHeight + 'px',
						//y: '+=' + 50 + 'px',
						ease: Power2.easeOut,
					}, 
					// 'poster-start'
					'start+=8'
				)
				.add('contentStart')
				.staggerFrom(staggerElements, 25,
					{
						// autoAlpha: 0,
						y: '+=' + this.$store.state.windowHeight + 'px',
						// ease: Power2.easeInOut,
						ease: Power1.easeInOut,
						// delay: -2, // delay per item
					},
					'0.25', // stagger item delay
					'poster-start' // start from 1.5 "dur" before label
				)
				.add('content-outro')
				.staggerTo(staggerElements, 15,
					{
						y: `-=${ this.$store.state.windowHeight }px`,
						// top: '-144px', // ROEN HOW DO I REMOVE THEM FREM THE SCENE
						// ease: Power4.easeIn,
						ease: Power2.easeIn,
						// delay: -2,
					},
					'-0.25' // making the order in the outro reverse-ish
				)

				.addLabel('content-end', 'content-outro+=13')
				.to(this.$refs.headline, 20, 
					{
						// y: `-=${ this.$store.state.windowHeight * .6}px`,
						y: `-=${ this.$store.state.windowHeight}px`,
						ease: Power4.easeOut,
						// delay: -1
					},
					'content-end',
				)
				.to(poster, 10,
					{
						autoAlpha: 0,
						ease: Linear.easeNone,
					},
					'content-outro+=11',
				)
				.fromTo(bg, 10,
					{
						autoAlpha: 1,
						ease: Linear.easeNone,
					},
					{
						autoAlpha: 0,
						ease: Linear.easeNone,
					},
					'content-outro+=11',
				)
				.to(this.$refs.headline, 0, { autoAlpha: 0 })
				.add('end')
				.progress(1, true) // cache animation, but don't do callbacks (the true parameter is suppressing events)
				.progress(0, true)
				;

			if(this.mq.md){ // COMMENT: media query check, same as on the animation-components in the template
				// trigger clover intro animations based on gsap introTl timeline labels
				this.introTl.addCallback(() => {
					if(scrollDirection == 'down'){
						this.$refs.clover1.setForward();
						this.$refs.clover1.play();
					} else {
						this.$refs.clover1.playOutro();
					}
				}, 'contentStart+=0.2');

				// // trigger clover outro animations based on gsap introTl timeline labels
				this.introTl.addCallback(() => {
					if(scrollDirection == 'down'){
						this.$refs.clover1.playOutro();
					} else {
						this.$refs.clover1.setForward();
						this.$refs.clover1.play();
					}
				}, 'content-end-=0.5');
			}
		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/settings';

.scene-content {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	min-height: 100vh;
	color: $color-white;
	opacity: 1;

	.clover-headline { // h2
		position: absolute;
		top: rem(210);
		// left: rem(197);
		left: 14%;
		width: 45%;
		max-width: rem(450);
		// transform: translateX(-50%);
		z-index: 16;
		display: block;

		font-family: $font-stack-bold;
		font-size: rem($title-1-min);
		text-align: left;
		line-height: 1.1;
		letter-spacing: -.6px;

		pointer-events: none;
		user-select: none;

		opacity: 0;
		visibility: hidden;

		@include mq($from: screen-sm){
			font-size: rem($title-1-mid);
			width: 39%;
			top: 26%;
			left: 5%;
		}
		@include mq($from: screen-md){
			top: 30%;	
		}
		@include mq($from: screen-lg){
			font-size: rem($title-1-max);
			//left: rem(800);
		}
		@include mq($from: screen-xl){
			width: 45%;
			left: 60%;
		}
	}

	.content {
		opacity: 0;
	}

	.clover {
		&-bubble {
			position: absolute;
			// right: 12%;
			// top: 20%;
			// font-size: rem(80);
			max-width: rem(400);
			font-weight: bold;
			z-index: 8;
		}
		&__poster-image {
			position: absolute;
			top: rem($main-menu-height);
			left: 0;
			opacity: 0;
			z-index: 2;

			@include mq($from: screen-md){
				top: rem($main-menu-height-md);
			}

			> img {
				height: calc(100vh - 50px);
			}
		}
	}
	
	.bubble { //
		position: absolute;
		top: 40%;
		// left: 40%;
		
		text-decoration: none;
	
		border-radius: 50%;
		text-align: center;
		z-index: 3;

		border-radius: 50%;

		overflow: hidden;
		@include mq($from: screen-sm){
			width: rem(234);
		}
		&::before {
			content: '';
			padding-top: 100%;
			height: 0;
			display: block;
		}
		> img {
			transition: transform .6s ease-in-out;
			position: absolute;
			top: 0;
			left: 0;
			max-width: 100%;

			&:hover {
				transform: scale(1.04);
			}
		}
		&--product {
			background: white;
			overflow: inherit;
			z-index: 5;
		}
		&--1 { // product
			min-width: rem(150);
			max-width: rem(338);
			width: calc(15vw * 1.4);
			left: 44%;
			// transform: translate3d(0%, 0%, 0);
			> img {
				top: -30%;
				left: -36%;
				width: 145%;
				max-width: none;
			}
			@include mq($from: screen-sm){
				left: 35%;
				//width: rem(205);
			}
			@include mq($from: screen-md){
				width: calc(15vw * 1.3);
			}
			@include mq($from: screen-lg){
				//left: 45%;
			}
			@include mq($from: screen-xl){
				left: 40%;
				//left: rem(400);
			}
		}
		&--2 { //leaves
			transform: translate3d(0%, 50%, 0); // y-position
			min-width: rem(217);
			max-width: rem(488);
			width: calc(20vw * 1.6);
			left: 31%;
			pointer-events: none;
			@include mq($from: screen-sm){
				transform: translate3d(0%, 0%, 0); // y-position
				left: 55%;
			}
			@include mq($from: screen-md){
				width: calc(20vw * 1.3);
				left: 55%;
			}
			@include mq($from: screen-lg){
				//left: rem(775);
			}
			@include mq($from: screen-xl){
				//width: rem(488);
				left: 60%;
			}
			
		
		}
		&--3 { // cowboys
			left: 60%;
			transform: translate3d(0%, -50%, 0);
			min-width: rem(217);
			max-width: rem(488);
			width: calc(20vw * 1.6);
			pointer-events: none;
			@include mq($from: screen-sm){
				left: 44%;
			}
			@include mq($from: screen-md){
				width: calc(20vw * 1.3);
			}
			@include mq($from: screen-lg){
				//left: rem(600);
			}
			@include mq($from: screen-xl){
				left: 51%;
				//width: rem(488);
			}
		}
		&--4 { // prduct two
			transform: translate3d(0%, 0%, 0);
			min-width: rem(117);
			max-width: rem(264);
			width: calc(15vw * 1.4);
			left: 63%;
			top: 70%;

			> img {
				top: -50%;
				left: -32%;
				position: absolute;
				max-width: none;
				width: 170%;
			}
			@include mq($from: screen-sm){
				left: 76%;
				top: 57%;
				//width: rem(193);
			}
			@include mq($from: screen-md){
				width: calc(15vw * 1.3);
			}
			@include mq($from: screen-lg){
				//left: 60%;
				
			}
			@include mq($from: screen-xl){
				left: 82%;
				//left: rem(1000);
				
			}
		}
	}
}
</style>



