<template>
	<section class="scene-content" :class="[className, inView ? 'active-pointer' : '']">
		<!-- <h2 class="scene-headline" ref="headline">Blossom content headline</h2> -->
		<div class="bg--half" ref="bg">
			<!-- <div class="image-wrapper"><img src="/static/images/blossom/blossom-content-background.jpg" alt=""></div> -->
		</div>

		<div class="all-texts" ref="texts">
			<div v-for="(product, index) in products" :class="['text', 'text--'+index]" :key="index">
				{{ $t(product.text) }}
			</div>
		</div>

		<div class="content content-list" ref="content">
			<div type="button" class="list-item custom-cursor" v-for="(product, index) in products" :key="index" @click="openClosedModal({sku: product.sku, target: $event.target})">
				<figure class="product-wrapper">
					<img :src="product.images.packshot" :alt="product.type" />
				</figure>
			</div>
		</div>

		<div class="visualizer"></div>
	</section>
</template>

<script>
import {mapActions } from 'vuex';
import ProgressMixin from '../mixins/ProgressMixin';

// import GSDevTools from 'GSDevTools';

import SceneHeadline from '../SceneHeadline';

export default {
	name: 'SceneContentBlossom',
	mixins: [ProgressMixin],
	components: {
		SceneHeadline,
	},
	props: {
		options: Object,
		className: String,
	},
	mounted(){
		// console.log('mounted - this.options.name: ', this.options.name);
		this.$nextTick(this.setupScene);
	},
	data(){
		return {
			// isAustralia: this.$i18n.locale === 'en_AU',
			// isNewZealand: this.$i18n.locale === 'en_NZ',
			mq: this.$store.state.mq,
			products: this.$store.state.products.filter(product => product.type === 'blossom' && product.marketUnavailability.indexOf(this.$i18n.locale) < 0),
		}
	},
	methods: {
		...mapActions([
			'openClosedModal',
		]),
		setupScene(){
			// 	content = this.$refs.content;

			this.introTl = new TimelineMax({id: 'blossom-content', paused: true}); // TMP - rename timeline for gsap debug

			// var bgMain = document.querySelector('.bg--main'); // set main bg to hidden
			// this.introTl
			// 	.set(bgMain, {autoAlpha: 0});

			var listItems = this.$refs.content.querySelectorAll('.list-item');
			var listItemsCount = listItems.length;

			// var texts = document.querySelectorAll('.all-texts .text');
			var texts = this.$refs.texts.querySelectorAll('.text');

			// var listItems = do;
			var content = this.$refs.content,
				contentHeight = content.clientHeight;

			// console.log('contentHeight: ', contentHeight);

			var bg = this.$refs.bg;

			var contentDuration = 100;
			var wh = this.$store.state.windowHeight;

			// // var itemEasingIn = CustomEase.create('itemEasingIn', 'M0,0 C0.456,0.456 0.713,0.842 0.774,0.902 0.874,1 0.942,1 1,1');
			// // var itemEasingIn = CustomEase.create('custom', 'M0,0 C0.51,0.348 0.556,0.306 0.718,0.642 0.899,1.018 0.948,1 1,1');
			// var itemEasingIn = CustomEase.create("custom", "M0,0,C0.32,0.38,0.556,0.561,0.646,0.716,0.81,1,0.84,1,1,1");
			// var itemEasingIn = CustomEase.create("custom", "M0,0 C0.112,0 0.112,0.05 0.192,0.148 0.197,0.154 0.679,0.67 1,1");
			// // var itemEasingOut = CustomEase.create('custom', 'M0,0 C0.112,0 0.15,0.076 0.3,0.43 0.462,0.814 0.922,0.952 1,1');
			// var itemEasingOut = CustomEase.create("custom", "M0,0 C0.112,0 0.298,0.002 0.378,0.1 0.414,0.144 0.606,0.464 1,1");

			this.introTl
				.set(content, {autoAlpha: 1}); // make sure content is shown before animating it's insides

			// function itemAnimStart(i){
			// 	console.log('animation started i = ', this);
			// }
			// function itemAnimComplete(i){
			// 	console.log('animation complete i = ', this);
			// }

			this.introTl
				.add('start')
				.fromTo(bg, (wh * 0.05), // 5% of timeline
					{
						// y: (wh / 5) + 'px', // 20 vh travel distance
						x: '-25%',
						autoAlpha: 0,
					},
					{
						// y: 0,
						x: '0%',
						autoAlpha: 1,
						ease: Linear.easeNone,
					}
				)
				.add('contentStart', 'start-=' + (wh * 0.05)) // content start point, referenced by the bubble's

				// new APPROACH
				.fromTo(content, wh,
					{
						y: wh + 'px',
					},
					{
						y: '-' + (contentHeight + 300) + 'px',
						ease: Linear.easeNone,
					}
				)
				;


			// var visualizerElem = document.querySelector('.visualizer');

			// nyStart - delay for item start in timeline
			var nyStart = (wh * 0.29); // (0px > 768px)
			var itemWidths = {
				mobile: [100, 155],
				tablet: [150, 250],
				desktop: [150, 300],
			};
			var mq = 'mobile';
			var itemWidth = itemWidths.mobile;
			if(this.mq.sm){
				mq = 'sm';
				// nyStart = (wh * 0.31); // 
				itemWidth = itemWidths.mobile;
			}
			if(this.mq.md){
				mq = 'md';
				// nyStart = (wh * 0.29); // 
				itemWidth = itemWidths.tablet;
			}
			if(this.mq.lg){
				mq = 'lg';
				// nyStart = (wh * 0.26); // 
				itemWidth = itemWidths.desktop;
			}
			if(this.mq.xxl){
				mq = 'xxl';
				// nyStart = (wh * 0.26); // 
				itemWidth = itemWidths.desktop;
			}
			if(this.mq.xxxl){
				mq = 'xxxl';
				// nyStart = (wh * 0.28); // 
				itemWidth = itemWidths.desktop;
			}

			[].forEach.call(listItems, function(item, i){
				var itemRelatedTextElem = texts[i];

				var itemTl = new TimelineMax({ id: 'itemtl-' + i, paused: false }); // nested timelines inherit master pause state
				var itemTextTl = new TimelineMax({ id: 'itemTexttl-' + i, paused: false }); // nested timelines inherit master pause state
				itemTl
					.set(item, {
						autoAlpha: 1,
						// scale: 0.5,
						width: itemWidth[0] + 'px',
						height: itemWidth[0] + 'px',
						// transformOrigin: '100% 50%',
					})
					// .to(item, 5,

					// 	// {
					// 	// 	y: wh + 'px',
					// 	// },
					// 	{
					// 		y: '-' + wh + 'px',
					// 		autoAlpha: 1,
					// 		// ease: itemEasingIn,
					// 		// ease: Power1.easeInOut,
					// 		ease: Linear.easeNone,
					// 		// ease: Power1.easeOut,
					// 	},
					// 	// 'contentStart+=' + itemIntroPosition
					// )
					// .to(item, (wh * 0.1), // when going for accumulated 20
					.to(item, (wh * 0.09), // when going for accumulated 18
						{
							// scale: 1,
							width: itemWidth[1] + 'px',
							height: itemWidth[1] + 'px',
							ease: Linear.easeNone,
						},
						// '-4'
					)
					// .to(item, (wh * 0.1), // when going for accumulated 20
					.to(item, (wh * 0.09), // when going for accumulated 20
						{
							// scale: 0.5,
							width: itemWidth[0] + 'px',
							height: itemWidth[0] + 'px',
							ease: Linear.easeNone,
						},
						// '-2'
					)
					;

				itemTextTl
					.set(itemRelatedTextElem, {
						autoAlpha: 0,
						// transformOrigin: '100% 50%',
					})
					// .to(item, 5,

					// 	// {
					// 	// 	y: wh + 'px',
					// 	// },
					// 	{
					// 		y: '-' + wh + 'px',
					// 		autoAlpha: 1,
					// 		// ease: itemEasingIn,
					// 		// ease: Power1.easeInOut,
					// 		ease: Linear.easeNone,
					// 		// ease: Power1.easeOut,
					// 	},
					// 	// 'contentStart+=' + itemIntroPosition
					// )
					// .fromTo(itemRelatedTextElem, (wh * 0.03), // when going for accumulated 20
					.fromTo(itemRelatedTextElem, (wh * 0.03), // when going for accumulated 18
						{
							y: '+5%',
							autoAlpha: 0,
						},
						{
							y: '0%',
							autoAlpha: 1,
							// ease: itemEasingIn,
							// ease: Power1.easeInOut,
							ease: Linear.easeNone,
							delay: (wh * 0.04), // when going for accumulated 20
						},
						// '-4'
					)
					.fromTo(itemRelatedTextElem, (wh * 0.03), // when going for accumulated 20
						{
							y: '0%',
							autoAlpha: 1,
						},
						{
							y: '-5%',
							autoAlpha: 0,
							// ease: itemEasingIn,
							// ease: Power1.easeInOut,
							ease: Linear.easeNone,

							 // COMMENT: This delay determines how long the text is at full visibility
							// delay: (wh * 0.06), // when going for accumulated 20
							delay: (wh * 0.04), // when going for accumulated 18
						},
						// '-2'
					)
					;

				// var itemStart = (nyStart + ((wh * .09) * i));
				// var itemTextStart = (nyStart + ((wh * .11) * i));
				var itemStart = (nyStart + ((wh * .09) * i));
				var itemTextStart = (nyStart + ((wh * .09) * i));
				// if(this.mq.lg){
				// 	itemStart = (nyStart + ((wh * .09) * i));
				// 	itemTextStart = (nyStart + ((wh * .11) * i));
				// }
				// if(this.mq.xxl){
				// 	itemStart = (nyStart + ((wh * .09) * i));
				// 	itemTextStart = (nyStart + ((wh * .11) * i));
				// }
				// console.log('--------------------');
				// console.log('itemStart: ', itemStart);
				// console.log('itemTextStart: ', itemTextStart);
				this.introTl
					.add(itemTl, 'contentStart+=' + itemStart)
					.add(itemTextTl, 'contentStart+=' + itemTextStart)
					;


				// this.introTl.timeScale(0.5);

				// visualizer setup
				// var visualizerDur = document.createElement('div');
				// visualizerDur.innerHTML = 'dur';
				// visualizerDur.classList.add('dur');
				// // visualizerDur.style.width = ((introOutroDuration * 2) * 10) + 'px';
				// visualizerDur.style.width = ((introOutroDuration ) * 10) + 'px';
				// visualizerDur.style.borderRightWidth = ((introOutroDuration ) * 10) + 'px';
				// visualizerDur.style.marginLeft = (introStartOffset * 10) + 'px';

				// visualizerElem.append(visualizerDur);
			}.bind(this));


			this.introTl
				.add('content-end')
				// .fromTo(bg, (contentDuration / 10),
				.to(bg, (wh * 0.05), // 5% of timeline
					// {
					// 	width: '50%', // changed from "fromTo" to "to" as for mobile is wider than 50% to start with (and media query is obsolete)
					// 	autoAlpha: 1,
					// 	// delay: -6
					// },
					{
						width: '100%',
						autoAlpha: 1,
						ease: Linear.easeNone,
						// delay: -7
						delay: -(wh * 0.25), // 15% of duration
					}
				)
				.to(bg, (wh * 0.04),
					// {
					// 	// width: '50%',
					// 	autoAlpha: 1,
					// 	// delay: -1
					// },
					{
						// width: '100%',
						autoAlpha: 0,
						ease: Linear.easeNone,
						// delay: (wh * 0.02),
					},
					'-=' + (this.$store.state.windowHeight * 0.20)
				)
				.add('end')
				.progress(1, true) // cache animation, but don't do callbacks (the true parameter is suppressing events)
				.progress(0, true)
				// .pause()
				;

			// GSAP Debug setup
			// GSDevTools.create({
			// 	// id: 'blossom content',
			// 	animation: this.introTl,
			// 	globalSync: false,
			// 	hideGlobalTimeline: true,
			// 	paused: true,
			// });

			// console.log('blossom introTl: ', this.introTl);
		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/settings';

.scene-content {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	min-height: 100vh;
	color: $color-white;
	opacity: 1;

	.content {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		visibility: hidden;
		z-index: 3;
		
		// padding-left: 25%;
		padding-left: 50%;

		@include mq($from: screen-sm){
			padding-left: 0;
		}
	}
	.all-texts {
		position: absolute;
		top: 35%;
		right: 50%;
		// left: rem(40);
		z-index: 4;
		max-width: rem(380);
		text-align: right;

		@include mq($from: screen-sm){
			right: 65%;
		}

		.text {
			position: absolute;
			top: 15%;
			// left: 0;
			right: 0vw;
			opacity: 0;
			visibility: hidden;

			// transform: translateY(-50%);

			font-size: rem(15);
			font-weight: normal;
			color: $color-text;
			line-height: 2;

			width: 45vw;

			@include mq($from: screen-sm){
				width: 32vw;
				font-size: rem(20);
			}
			@include mq($from: screen-md){
				width: rem(380);
				font-size: rem(25);
			}
		}
		.cta { // a-tag
			position: relative;
			display: block;
			color: $color-white;
			margin-top: rem(20);
		}
	}
	.list-item {
		// position: absolute;
		// top: 50%; // old value
		// // top: 100%;
		// left: 0;
		// width: 100%;
		text-align: center;

		// transform: translateY(-50%) scale(1.5);

		opacity: 1;
		visibility: visible;

		margin: 0 auto rem(50);

		// transform: scale(0.5);
		width: rem(100);
		height: rem(100);

		@include mq($from: screen-md){
			width: rem(150);
			height: rem(150);
		}

		.product-wrapper {
			position: relative;
			margin: auto;
			background-color: $color-white;
			border-radius: 50%;
			// width: rem(250);
			// height: rem(250);
			width: 100%;
			height: 100%;

			overflow: hidden;

			img {
				position: absolute;
				top: 0;
				left: 0;
				// transform: translate(-57%, -25%);
				width: 100%;
			}

			// margin-bottom: rem(240);
		}

		// border: rem(1) solid white;
		// &:nth-child(1) {
		// 	border-color: red;
		// }
		// &:nth-child(2) {
		// 	border-color: green;
		// }
		// &:nth-child(3) {
		// 	border-color: blue;
		// }
	}

	.bg--half {
		position: fixed;
		top: 0;
		left: 0;
		// width: 100%;
		height: 100vh;
		opacity: 1;
		background-color: $color-carousel-pink;

		z-index: 2;

		pointer-events: none;
		user-select: none;

		width: 75%;

		@include mq($from: screen-sm){
			width: 50%;
		}

		// .image-wrapper {
		// 	position: absolute;
		// 	top: 0;
		// 	right: 0;
		// 	height: 100%;
		// 	width: 50%;
		// 	overflow: hidden;

		// 	img {
		// 		object-fit: cover;
		// 		object-position: 50% 50%;
		// 	}
		// }
	}
}

</style>



