<template>
	<div class="modal-overlay" @click="closeOpenModal" ref="overlay" :class="[showOverlay ? 'animated' : '']">
		<div class="modal" tabindex="0" @keyup.esc="closeOpenModal" @click.stop ref="modal" :class="[hasLoaded ? 'loaded' : '']">
			<div class="modal-closeButton" @click="closeOpenModal"></div>
			<picture v-if="modal.isCard" class="card-modal-image">
				<source media="(max-width: 566px)" :srcset="modal.content.images.cardSmall">
				<source media="(min-width: 567px)" :srcset="modal.content.images.cardLarge">
				<img :src="modal.content.images.cardLarge" @load="loaded">
			</picture>
			<picture v-if="!modal.isCard" class="modal-image__lifestyle">
				<source media="(max-width: 566px)" :srcset="modal.content.images.lifestyleSmall">
				<source media="(min-width: 567px)" :srcset="modal.content.images.lifestyleLarge">
				<img :src="modal.content.images.lifestyleLarge" @load="loaded">
			</picture>
			<picture v-if="!modal.isCard" class="modal-image__packshot">
				<img :src="modal.content.images.packshot" @load="loaded" :srcset="`${modal.content.images.packshotLarge} 500w`">
			</picture>
			<div class="modal-content">
				<div class="modal-price" v-if="!modal.isCard">{{ $t(`${modal.content.sku}_price`) }}</div>
				<!-- // <a href="javascript:;" class="modal-link button" @click="registerProductClick(modal.content.sku)" target="_blank">{{ $t('general_shop_now') }}</a> -->
				<a v-if="modal.sceneType !== 'butterfly'" href="javascript:;" class="modal-link button" @click="trackShopNowClick(modal.content.sku)" target="_blank">{{ $t('general_shop_now') }}</a>

				<a v-if="modal.sceneType === 'butterfly' && modal.content.linkType === 'collectionpage'" href="javascript:;" class="modal-link button" @click="trackCollectionClick(modal.content.sku)" target="_blank">{{ $t('general_shop_now') }}</a>

				<a v-if="modal.sceneType === 'butterfly' && modal.content.linkType !== 'collectionpage' && modal.content.linkType !== ''" href="javascript:;" class="modal-link button" @click="trackShopNowClick(modal.content.linkType)" target="_blank">{{ $t('general_shop_now') }}</a>
			</div>
		</div>
	</div>
</template>

<script>
	import { mapState, mapActions } from 'vuex';
	import { TimelineMax, Power4, Power1 } from 'gsap';

	export default {
		name: 'Modal',
		computed: mapState([
			'modal',
		]),
		data(){
			return {
				mq: this.$store.state.mq,
				numberLoaded: 0,
				hasLoaded: false,
				showOverlay: false,
			};
		},
		created(){
			this.modalOpenTl = new TimelineMax();
		},
		// mounted() {
		// 	// this.position = this.$store.state.modal.position;
		// },

		methods: {
			...mapActions([
				'closeOpenModal',
				// 'registerShopClick',
				'registerProductClick',
			]),
			trackShopNowClick(sku) {
				this.$store.dispatch({
					type: 'registerProductClick',
					product: {
						sku: sku,
						id: sku,
						name: this.$t(`${sku}_name`),
						description: this.$t(`${sku}_description`),
						price: this.$t(`${sku}_price`).replace(/[^\d\.\,]/g, ''),
					},
				});
			},
			trackCollectionClick(sku) {
				// this.$store.dispatch({
				// 	type: 'registerProductClick',
				// 	product: {
				// 		sku: sku,
				// 		id: sku,
				// 		name: this.$t(`${sku}_name`),
				// 		description: this.$t(`${sku}_description`),
				// 		price: this.$t(`${sku}_price`),
				// 	},
				// });

				// PLAEASE NOTE - BELOW IS SPECIAL TRACKING FOR BUTTERFLY MODAL LINKS
				if(this.$store.state.debugTracking){
					console.log('Tracking - Registered shop collection click.');
				}
				if(window.utag){
					// COMMENT: Gather all tracking info
					var allSkuNumbers = [],
						allProductNames = [],
						allProductPrices = [],
						allProductCategories = [],
						allProductColors = [],
						allProductUrls = [],
						allProductDescs = [],
						allProductQuantities = [];

					// console.log('locale: ', this.$i18n.locale);
					this.$store.state.products.forEach(product => {
						if(product.hideInMenu === false){

							var sku = product.sku,
								name = this.$t(`${product.sku}_name`),
								desc = this.$t(`${product.sku}_description`),
								price = this.$t(`${product.sku}_price`).replace(/[^\d\.\,]/g, '');

							if(this.$store.state.debugTracking){
								// console.log('menu - price var: ', price);
								console.log('menu - price: ', price);
								console.log('name: ', name);
								console.log('desc: ', desc);
							}

							allSkuNumbers.push(sku);
							allProductNames.push(name);
							allProductPrices.push(price);
							allProductDescs.push(desc);

							allProductQuantities.push('1');
							allProductCategories.push('');
							allProductUrls.push('');
							allProductColors.push('');
						}
					});

					window.utag.link({
						// eCapacity comment: product data of all involved products  see this section for further instructions
						product_sku: allSkuNumbers,
						product_id: allSkuNumbers,
						product_name: allProductNames,
						product_price: allProductPrices,
						product_unit_price: allProductPrices,
						product_quantity: allProductQuantities,

						// Empty but necessary
						product_category: allProductCategories,
						product_color: allProductColors,
						product_url: allProductUrls, // (S/H add place holder and generate url based on same URL that shop now points to)
						product_image_url: allProductUrls,

						// estore_event: ['Pandora Garden shop the collection'],
						estore_event: ['Pandora Garden shop now'],
					});
				}
				window.open(this.$t('endscene_cta_link'), '_blank');
			},
			openModal() {
				// if(this.modal.content.contentType === 'video') return; // COMMENT: IS TEST IN ACTIONS.JS INSTEAD
				const modalCon = this.$refs.modal;
				modalCon.focus(); // COMMENT: Setting auto-focus, otherwise the esc-keyup event will not fire(combined with tabindex=0)

				this.modalOpenTl.set(modalCon, {
					left: Math.floor(this.modal.coords.left),
					top: Math.floor(this.modal.coords.top),
					width: Math.floor(this.modal.coords.width),
					height: Math.floor(this.modal.coords.height),
					xPercent: 0,
					yPercent: 0,
					
					opacity: 1,
				});
				this.modalOpenTl
					.add('start')
					.to(modalCon, 1.1,
						{
							// position: 'absolute',
							height: 'auto',
							width: '90%',
							left: '50%',
							top: '50%',
							xPercent: -50,
							yPercent: -50,
							ease: Power1.easeOut,
							// delay: .1
						}
					)
					;
				this.modalOpenTl.addCallback(()=>{
					this.showOverlay = true;
				}, 'start+=0.3');
			},
			openMobile() {
				// if(this.modal.content.contentType === 'video') return; // COMMENT: IS TEST IN ACTIONS.JS INSTEAD
				const modalCon = this.$refs.modal;
				TweenMax.set(modalCon, {
					height: '100%',
					left: '50%',
					top: '50%',
					width: '100%',
					xPercent: -50,
					yPercent: -50,
					opacity: 1,
				});
				TweenMax.from(modalCon, 0.8, {
					// position: 'absolute',
					left: Math.floor(this.modal.coords.left),
					top: Math.floor(this.modal.coords.top),
					width: Math.floor(this.modal.coords.width),
					height: Math.floor(this.modal.coords.height),
					xPercent: 0,
					yPercent: 0,
					ease: Power1.easeOut,
					onComplete: () => {
						this.showOverlay = true;
					},
				});
			},
			loaded() {
				// if(this.modal.content.contentType === 'video') return; // COMMENT: IS TEST IN ACTIONS.JS INSTEAD
				this.numberLoaded++;
				if (this.numberLoaded === 1) {
					this.hasLoaded = true;
					window.innerWidth >= 567 ? this.openModal() : this.openMobile();

					if(this.$store.state.debugTracking){
						console.log('Tracking - Registered modal open. Sku: ', this.modal.content.sku);
					}
					if(window.utag && this.modal.content.sku){
						var sku = this.modal.content.sku,
							name = this.$t(`${sku}_name`),
							desc = this.$t(`${sku}_description`),
							price = this.$t(`${sku}_price`).replace(/[^\d\.\,]/g, '');

						if(this.$store.state.debugTracking){
							console.log('modal open - sku: ', sku);
							console.log('modal open - name: ', name);
							console.log('modal open - price: ', price);
							console.log('modal open - desc: ', desc);
						}
							
						window.utag.link({
							// product data of all involved products  see this section for further instructions
							product_sku: [sku],
							product_id: [sku],
							product_name: [name],
							product_price: [price],
							product_unit_price: [price],
							product_quantity: ['1'],
							product_description: [desc],

							// Empty but necessary
							product_category: [''],
							product_color: [''],
							product_url: [''], // (S/H add place holder and generate url based on same URL that shop now points to)
							product_image_url: [''],

							estore_event: ['Pandora Garden product overlay', 'product detail view']
						});
					}
				} else {
					// closing modal
				}
			},
		},
	};
</script>

<style lang="scss" scoped>
	@import '../assets/settings';

	.modal-overlay {
		position: fixed;
		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		background-color: rgba(169, 115, 115, 0);
		transition: background-color 1s ease;
		z-index: 9999;

		&.animated {
			background-color: rgba(0, 0, 0, .6);
		}
	}

	.modal {
		position: absolute;
		top: 50%;
		left: 50%;
		display: flex;
		align-items: stretch;
		width: 90%;
		height: auto;
		max-width: rem(1500);

		background-color: $color-white;
		opacity: 0;

		&:focus { // COMMENT: Setting auto-focus, otherwise the esc-keyup event will not fire
			outline: 0;
		}

		picture {
			flex: 0 0 50%;

			&.card-modal-image {
				display: block;
				flex: 0 0 100%;
			}

			img {
				display: block;
				width: 100%;
				height: 100%;
				object-fit: cover;
			}

			&.packshot {
				// height: 75%;

				img {
					// max-height: 75%;
				}
			}

			&.modal-image__packshot {
				padding-top: rem(30);
				// height: 75%;

				@include mq($from: screen-md) {
					padding-top: 0;
				}

				img {
					max-height: 80%;
				}
			}
		}

		@include mq($until: screen-xs) {
			flex-wrap: wrap;
			align-items: flex-start;
			align-content: flex-start;

			picture {
				flex: 0 0 100%;
				height: 40vh;

				&:nth-of-type(2) {
					height: 40vh;

					img {
						object-fit: contain;
					}
				}
				&.card-modal-image {
					height: 100%;
					display: block;
				}
			}
		}
	}

	.modal-closeButton {
		position: absolute;
		top: rem(30);
		right: rem(30);
		height: rem(40);
		width: rem(40);

		background-color: $color-white;
		border-radius: 50%;
		box-shadow: rem(10) rem(10) rem(40) rgba(0, 0, 0, .1);
		transition: opacity 1s ease;
		opacity: 0;
		cursor: pointer;

		.animated & {
			opacity: 1;
		}

		&:before, &:after {
			content: '';
			position: absolute;
			left: 50%;
			top: 50%;
			display: block;
			height: rem(2);
			width: rem(25);
			transform: translate(-50%) rotate(45deg);

			border-radius: rem(2);
			background-color: black;
		}

		&:after {
			transform: translate(-50%) rotate(-45deg);
		}
	}

	.modal-content {
		position: absolute;
		// box-sizing: border-box;
		right: 0;
		bottom: 0;
		padding: rem(30);
		opacity: 0;
		transition: opacity 1s ease;

		@include mq($until: screen-xs) {
			width: 100%;
		}

		.animated & {
			opacity: 1;
		}

		.button {
			background-color: $color-white;
		}

		picture:focus,
		img:focus {
			outline: 0;
		}
	}

	.modal-price {
		color: $color-nevada;
		font-size: rem(30);
		font-family: $font-stack;
		letter-spacing: rem(2);
		text-align: right;
		margin-bottom: rem(20);

		@include mq($until: screen-xs) {
			text-align: center;
			font-size: rem(15);
			margin-bottom: rem(15);
		}
	}
	.button {
		box-shadow: none;
		border: rem(1) solid #333;
	}
</style>



