<script>
// import {mapState, mapActions } from 'vuex';
import ProgressMixin from '../mixins/ProgressMixin';

// import GSDevTools from 'GSDevTools';
// import SplitText from 'GSSplitText';

import Hero from '../Hero';
import SceneHeadline from '../SceneHeadline';

export default {
	name: 'SceneHero',
	mixins: [ProgressMixin],
	components: {
		Hero,
		SceneHeadline,
	},
	props: {
		heroType: {
			type: String,
			default: 'video',
		},
		heroSrc: {
			type: String,
		},
		headline: {
			type: String,
		},
		className: String,
		options: Object,
		firstHero: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			mq: this.$store.state.mq,
			tl: null,
			IEorEdge: this.$store.state.IEorEdge
		};
	},
	created(){
		if(this.options.contentId === 'butterflyHero'){
			this.introTl = null; // COMMENT: outroTl is used instead
			this.tl = new TimelineMax({ paused: true }); // COMMENT: Headline intro animation (overwrite mixin default)
		} else {
			this.outroTl = null; // reset what comes from mixin to disable outro animation
		}
	},
	mounted(){
		// console.log('mounted - this.options.name: ', this.options.name);
		this.$nextTick(this.setupScene);
		// console.log('scene name: ', this.options.name);

		this.heroVideo = document.querySelector('#main-video');
		this.heroVideoSpinner = document.querySelector('.video-spinner');

		if(this.firstHero){
			// COMMENT: Video is ready for play, and (if needed) intro can proceed
			// COMMENT: Needs test on 3G
			// COMMENT: 'canplaythrough' is called on each loop/repeat of video
			
			// this.heroVideo.addEventListener('canplaythrough', this.videoCanplaythroughHandler, false);
			this.heroVideo.addEventListener('canplay', this.videoCanplayHandler, false);

			setTimeout(()=>{ // COMMENT: Fallback for when canplay is already called before we have attached the listener, or really bad network
				if(this.$store.state.firstVideoReady === false){
					this.$store.state.firstVideoReady = true;
					// this.$emit('firstVideoReady', true);
				}
			}, this.$store.state.introDoneFallbackTime);
		}

		this.$refs.sceneHero.style.height = this.$store.state.windowHeight;
	},

	methods: {
		// ...mapActions([
		// 	'registerShopClick',
		// ]),
		trackCollectionClick() {
			if(this.$store.state.debugTracking){
				console.log('Tracking - Registered shop collection click.');
			}
			if(window.utag){
				// COMMENT: Gather all tracking info
				var allSkuNumbers = [],
					allProductNames = [],
					allProductPrices = [],
					allProductCategories = [],
					allProductColors = [],
					allProductUrls = [],
					allProductDescs = [],
					allProductQuantities = [];

				// console.log('locale: ', this.$i18n.locale);
				this.$store.state.products.forEach(product => {
					if(product.hideInMenu === false){

						var sku = product.sku,
							name = this.$t(`${product.sku}_name`),
							desc = this.$t(`${product.sku}_description`),
							price = this.$t(`${product.sku}_price`).replace(/[^\d\.\,]/g, '');

						if(this.$store.state.debugTracking){
							console.log('hero - price: ', price);
							console.log('name: ', name);
							console.log('desc: ', desc);
						}

						allSkuNumbers.push(sku);
						allProductNames.push(name);
						allProductPrices.push(price);
						allProductDescs.push(desc);

						allProductQuantities.push('1');
						allProductCategories.push('');
						allProductUrls.push('');
						allProductColors.push('');
					}
				});

				window.utag.link({
					// eCapacity comment: product data of all involved products  see this section for further instructions
					product_sku: allSkuNumbers,
					product_id: allSkuNumbers,
					product_name: allProductNames,
					product_price: allProductPrices,
					product_unit_price: allProductPrices,
					product_quantity: allProductQuantities,

					// Empty but necessary
					product_category: allProductCategories,
					product_color: allProductColors,
					product_url: allProductUrls, // (S/H add place holder and generate url based on same URL that shop now points to)
					product_image_url: allProductUrls,

					estore_event: ['Pandora Garden shop the collection'],
				});
			}
			window.open(this.$t('endscene_cta_link'), '_blank');

		},
		videoCanplayHandler(e){
			// console.log('e: ', e);
			if(this.$store.state.firstVideoReady === false){
				this.$store.state.firstVideoReady = true;
				// this.$emit('firstVideoReady', true);
				// this.heroVideo.removeEventListener('canplaythrough', this.videoCanplaythroughHandler, false);
				this.heroVideo.removeEventListener('canplay', this.videoCanplayHandler, false);
			}
		},
		handleVideoClick(){
			// console.log('video click..................');
			if(this.heroVideo.muted){
				this.heroVideo.muted = false;

				if(this.$store.state.trackUnmute){
					if(this.$store.state.debugTracking){
						console.log('Tracking - Registered unmute.');
					}
					if(window.utag){
						window.utag.link({
							estore_event: ['Pandora Garden branded video unmute'],
						});
					}
				}

			} else {
				this.heroVideo.muted = true;
			}
			this.$refs.soundIcon.classList.toggle('unmuted');
			// this.heroVideo.volume = 1.0;
		},
		playIntro(){ // called by app.vue if state.showIntro is false
			// this.tl.tweenFromTo('start', 'end');

			// COMMENT: Tiny delay in play to prevent headlines clashing
			this.tl.delay(.2).play();
			this.runPrepare();
			// this.introTl.play();
			// GSDevTools.create({id: 'dsadas'});
		},
		setupScene(){
			if(this.options.contentId === 'butterflyHero'){
				this.setupFirstHeroAnimations();
			} else {
				this.setupAnimations();
			}
		},
		runPrepare(prepareProgress){
			// COMMENT: Used when only one video-tag
			// ESBEN COMMENT: would had been nice to move the video element to it's own container and use the state instead!
			const currentSrc = this.heroVideo.src;
			if(this.IEorEdge) {
				this.heroVideo.poster = this.options.videoPoster;
				this.heroVideo.src = this.$store.state.scenes.filter(sc => sc.contentId == 'brandedVideo')[0].heroSrc.desktopSmall;
			} else if(typeof this.options.heroSrc === 'string'){
				if(currentSrc !== this.options.heroSrc){
					this.heroVideo.poster = this.options.videoPoster;
					this.heroVideo.src = this.options.heroSrc;
				}
			} else { // COMMENT: heroSrc is (at the time of writing) only an object in relation to the BrandedVideo
				if(this.mq.portrait === true && this.mq.md === false){
					if(currentSrc !== this.options.heroSrc.mobile){
						this.heroVideo.poster = this.options.videoPoster;
						this.heroVideo.src = this.options.heroSrc.mobile;
					}
				} else if(this.mq.xxl === true){
					if(currentSrc !== this.options.heroSrc.desktopLarge){
						this.heroVideo.poster = this.options.videoPoster;
						this.heroVideo.src = this.options.heroSrc.desktopLarge;
					}
				} else if(this.mq.md === true){
					if(currentSrc !== this.options.heroSrc.desktopSmall){
						this.heroVideo.poster = this.options.videoPoster;
						this.heroVideo.src = this.options.heroSrc.desktopSmall;
					}
				}
			}
			this.heroVideo.play();
			this.isVideoLoading();

			// COMMENT: Used with multiple video-tags
			// var oldVideoContainer = document.querySelector('.video-active');
			// oldVideoContainer.pause();
			// oldVideoContainer.classList.remove('video-active');
			// var newVideoContainer = document.querySelector(this.options.videoContainer);
			// newVideoContainer.play();
			// newVideoContainer.classList.add('video-active');
		},
		isVideoLoading(){
			this.heroVideoSpinner.classList.add('loading');
			var videoInterval = setInterval(()=>{
				// console.log('this.heroVideo.readyState: ', this.heroVideo.readyState);
				if(this.heroVideo.readyState === 4){
					// do whatever you want done here
					// you can now stop checking every half second
					this.heroVideoSpinner.classList.remove('loading');
					// this.heroVideoFilter.style.opacity = 1;
					clearInterval(videoInterval);
				} else {
					if(this.heroVideoSpinner.classList.contains('loading') === false){
						// this.heroVideoFilter.style.opacity = 0;
						this.heroVideoSpinner.classList.add('loading');
					}
				}
			}, 100);
		},
		setupFirstHeroAnimations(){ // Butterfly Hero
			var bg = document.querySelector('.bg--intro');
			var introDuration = 2;

			// ------------------
			// ------------- NOTE
			// STRICT order in defining the gsap timelines(outro before tl), in order to get the right "first print" animation state!
			// ------------------

			const arrowWrapper = this.$refs.arrowWrapper; // for the outro
			// const arrows = [this.$refs.arrow1, this.$refs.arrow2];
			const gestures = [this.$refs.gesture1, this.$refs.gesture2];
			const mouse = this.$refs.indicatorMouse;
			const mouseWheel = this.$refs.indicatorMouseWheel;
			const arrowCircle = this.$refs.arrowCircle;
			// this.scrollIndicatorTl = new TimelineMax({
			// 	paused: false,
				

			// 	// onComplete:()=>{
			// 	// 	// console.log('completed - this: ', this);

			// 	// 	// TODO: SCROLL INDICATOR REPEAT
			// 	// 	// this.scrollIndicatorTl.tweenFromTo('repeatPoint', 'repeatEnd').delay(2).play();
			// 	// },
			// 	// onCompleteParams:['{self}'],
			// });
			this.scrollIndicatorTl = new TimelineMax({ paused: false });
			this.circleTl = new TimelineMax({
				paused: false,
			});

			if(this.IEorEdge === false){
				this.circleTl // animate the circle background
					.to(arrowCircle, 0.5,
						{
							autoAlpha: 1,
							ease: Linear.easeNone,
						}
					)
					;
				if(window.matchMedia('(max-width: 1023px)').matches){ // COMMENT: Mobile, animate hand-icon(gesture)'s
					this.scrollIndicatorTl = new TimelineMax({ paused: false, repeat: -1, repeatDelay: 3 });
					this.scrollIndicatorTl
						.fromTo(gestures[0], 1,
							{
								y: '-10%',
								x: '-50%',
								autoAlpha: 0,
							},
							{
								y: '-50%',
								x: '-50%',
								autoAlpha: 1,
								delay: -0.2,
								ease: Power2.easeInOut,
							}
						)
						.to(gestures[0], .5,
							{
								autoAlpha: 0,
							},
						)
						.fromTo(gestures[1], 1,
							{
								y: '-10%',
								x: '-50%',
								autoAlpha: 0,
							},
							{
								delay: -0.4,
								y: '-50%',
								x: '-50%',
								autoAlpha: 1,
								ease: Power2.easeInOut,
							}
						)
						.to(gestures[1], .5,
							{
								autoAlpha: 0,
							},
						)
						.fromTo(gestures[0], 1,
							{
								y: '-10%',
								x: '-50%',
								autoAlpha: 0,
							},
							{
								delay: -0.4,
								y: '-50%',
								x: '-50%',
								autoAlpha: 1,
								ease: Power2.easeInOut,
							}
						)
						;

				} else { // COMMENT: Desktop, animate mousewheel
					this.scrollIndicatorTl = new TimelineMax({ paused: false, repeat: -1, repeatDelay: 2 });
					this.circleTl
						.fromTo(mouse, 1,
							{
								y: '-50%',
								x: '-50%',
								autoAlpha: 0,
							},
							{
								y: '-50%',
								x: '-50%',
								autoAlpha: 1,
								delay: -0.2,
								ease: Power2.easeInOut,
							}
						);
					this.scrollIndicatorTl
						.fromTo(mouseWheel, .5,
							{
								y: '+=0%',
							},
							{
								y: '+=100%',
							},
						)
						.fromTo(mouseWheel, 1,
							{
								y: '+=0%',
							},
							{
								y: '-=100%',
							},
						)
						.fromTo(mouseWheel, .5,
							{
								y: '+=0%',
							},
							{
								y: '+=100%',
							},
						)
						.fromTo(mouseWheel, 1,
							{
								y: '+=0%',
							},
							{
								y: '-=100%',
							},
						)
						;
				}
			}

			this.outroTl
				.add('start')
				.fromTo(this.$refs.headline.$el, 1,
					{ 
						y: '-50%',
						autoAlpha: 1,
					},
					{ 
						y: '-=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
						autoAlpha: 0,
						ease: Linear.easeNone,
					}
				)
				.to(arrowWrapper, 0.5,
					{
						autoAlpha: 0,
					},
					'start+=0'
				)
				.add('end')
				.progress(1, true) // cache animation, but don't do callbacks (the true parameter is suppressing events)
				.progress(0, true) // set start pos back to 0
				// .pause()
				;
			

			// ------------------
			// ------------- NOTE
			// STRICT order in defining the gsap timelines(outro before tl), in order to get the right "first print" animation state!
			// ------------------

			this.tl
				.add('start')
				.fromTo(this.$refs.headline.$el, introDuration, 
					{ 
						y: '+=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
						autoAlpha: 0,
					},
					{ 
						y: '-50%',
						autoAlpha: 1,
						// ease: Linear.easeNone,
						ease: Power2.easeInOut,
					})
				.fromTo(
					bg,
					1.5, 
					{ 
						// y: '-=' + ((this.windowHeight / 100) * this.headlineAnimationDistance) + 'px',
						autoAlpha: 1,
						// delay: -introDuration,
					}, 
					{ 
						// y: '-=' + ((this.windowHeight / 100) * this.headlineAnimationDistance) + 'px',
						autoAlpha: 0,
						// delay: -introDuration,
						ease: Linear.easeNone,
					},
					'start'
				)
				.add('end')
				;

			// COMMENT: Added the scroll indicator circle and icon animations to the "main"-timeline
			if(this.IEorEdge === false){
				this.tl
					.add(this.circleTl, 'end-=0.5', 'circle')
					.add(this.scrollIndicatorTl, 'end+=0.2', 'indicator');
			}

			this.tl
				.progress(1, true) // cache animation, but don't do callbacks (the true parameter is suppressing events)
				.progress(0, true) // set start pos back to 0
				// .pause()
				;

			this.tl
				.addCallback(() => {
					if(this.$store.state.introHijackScroll === true && !this.$store.state.IEorEdge){
						this.$store.commit({ type: 'disableScroll', bool: false })
						// document.body.style.height = this.$store.state.masterHeight + 'px';
						// document.body.style.maxHeight = 'none';
						// document.body.style.overflow = 'auto';
					}
				}, 'end');
			this.tl
				.addCallback(() => {
					// COMMENT: Some display of the headline occurs, so this 'hidden' fixes it
					this.$refs.headline.$el.classList.remove('hidden'); // COMMENT: Class is only added to "firstHero", in above template
				}, 'start');

			// console.log('butter hero tl: ', this.tl);
		},
		setupAnimations(){
			var progress = 0;
			var scrollDirection = 'down';
			// var bg = document.querySelector('.bg--main');

			this.introTl = new TimelineMax({paused: true, onUpdateParams: ['{self}'], onUpdate: function(introTl) {
				var currentProgress = introTl.progress();
				if(currentProgress > progress){
					scrollDirection = 'down'
				} else {
					scrollDirection = 'up'
				}
				progress = introTl.progress();
			}});

			// ------------ STANDARD HEADLINE ANIM
			// this.introTl
			// 	.add('start')
			// 	// ----- Linear easing
			// 	.fromTo(this.$refs.headline.$el, 1, 
			// 		{ 
			// 			y: '+=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
			// 			autoAlpha: 0,
			// 		},
			// 		{ 
			// 			y: '-50%',
			// 			autoAlpha: 1,
			// 			ease: Power2.easeIn,
			// 			// ease: Linear.easeNone,
			// 		}
			// 	)
			// 	.add('50')
			// 	.to(this.$refs.headline.$el, 1,
			// 		{ 
			// 			y: '-=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
			// 			autoAlpha: 0,
			// 			delay: 1,
			// 			ease: Power2.easeIn,
			// 			// ease: Linear.easeNone,
			// 		}
			// 	)
			// 	.add('end')
			// 	.progress(1) // caching animation
			// 	.progress(0) // set stort pos back to 0
			// 	// .pause()
			// 	;

			// ------------------------------------------------ STD END


			// ---------- NEW SPLITTEXT / STAGGER ANIM
			var headlineManchet = [];
			if(this.$refs.headline.$refs.headline){
				headlineManchet.push( this.$refs.headline.$refs.headline );

				// COMMENT: Below code is "split-text'ing" the headline (not desired at this time)
				// mySplitText = new SplitText("#quote", {type:"words,chars"})
				// var mySplitText = new SplitText(this.$refs.headline.$refs.headline, {type:'words'});
				// console.log('mySplitText: ', typeof mySplitText.words);
				// [].forEach.call(mySplitText.words, function(word, i){
				// 	headlineManchet.push(word);
				// });
				// headlineManchet.reverse();
			}
			if(this.$refs.headline.$refs.manchet){
				headlineManchet.push( this.$refs.headline.$refs.manchet );
			}
			if(headlineManchet.length > 0){
				this.introTl
					.set(this.$refs.headline.$el, {autoAlpha: 1});
				this.introTl
					.add('start')
					.staggerFromTo(headlineManchet, 1, 
						{ 
							y: '+=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
							autoAlpha: 0,
						},
						{ 
							y: '-50%',
							autoAlpha: 1,
							ease: Power1.easeOut,
							// ease: Linear.easeNone,
						},
						'.1'
					)
					.add('halfway')
					.staggerTo(headlineManchet, 1,
						{ 
							y: '-=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
							autoAlpha: 0,
							// delay: .1,
							ease: Power2.easeIn,
							// ease: Linear.easeNone,
						},
						'.1',
						'halfway+=0'
					)
					.add('end')
					.progress(1, true) // cache animation, but don't do callbacks (the true parameter is suppressing events)
					.progress(0, true) // set start pos back to 0
					// .pause()
					;

				if(this.$store.state.trackSceneChange){
					this.introTl.addCallback(()=>{
						if(this.options.sceneChangeTracked === false){
							if(this.$store.state.debugTracking){
								console.log('Tracking - Registered scene change, scene: ', this.options.name);
							}
							if(window.utag){
								window.utag.link({
									estore_event: ['Pandora Garden scene change: ' + this.options.name],
								});
							}
							this.options.sceneChangeTracked = true;
						}
					}, 'halfway');
				}
			} else {
				// COMMENT: This "else" is for the scene BrandedVideo which has no headline or manchet, but the "headline-wrapper"
				// we still need this timeline to be able to control the app-frame
				this.introTl
					.add('start')
					// ----- Linear easing
					.fromTo(this.$refs.soundIcon, .4,
						{
							yPercent: '+10%',
							autoAlpha: 0,
						},
						{
							yPercent: '0%',
							autoAlpha: 1,
							ease: Power2.easeOut,
						},
					)
					.fromTo(this.$refs.headline.$el, .5, 
						{ 
							y: '+=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
							autoAlpha: 0,
						},
						{ 
							y: '-50%',
							autoAlpha: 1,
							ease: Power2.easeIn,
							// ease: Linear.easeNone,
						},
						'start'
					)
					.add('50')
					.to(this.$refs.headline.$el, .5,
						{ 
							y: '-=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
							autoAlpha: 0,
							delay: .2,
							ease: Power2.easeIn,
							// ease: Linear.easeNone,
						},
					)
					.add('end')
					.fromTo(this.$refs.soundIcon, .4,
						{
							yPercent: '0%',
							autoAlpha: 1,
						},
						{
							yPercent: '+10%',
							autoAlpha: 0,
							ease: Power2.easeOut,
						},
					)
					.progress(1, true) // cache animation, but don't do callbacks (the true parameter is suppressing events)
					.progress(0, true)
					// .pause()
					;

				if(this.$store.state.trackSceneChange){
					this.introTl.addCallback(()=>{
						if(this.options.sceneChangeTracked === false){
							if(this.$store.state.debugTracking){
								console.log('Tracking - Registered scene change, scene: ', this.options.name);
							}
							if(window.utag){
								window.utag.link({
									estore_event: ['Pandora Garden scene change: ' + this.options.name],
								});
							}
							this.options.sceneChangeTracked = true;
						}
					}, '50');
				}
			}
			// ----------------------------------------------- NEW SPLITTEXT END

			// if branded-video hero, control visibility of the app-frame
			if(this.options.name == 'branded-video'){
				this.introTl.addCallback(() => {
					// console.log('scrollDirection: ', scrollDirection);
					if(scrollDirection == 'down'){
						this.$store.commit({
							type: 'showMenu',
							hide: true
						});
					} else {
						this.$store.commit({
							type: 'showMenu',
							hide: false
						});
					}
				}, 'start+=0.1');
				this.introTl.addCallback(() => {
					if(scrollDirection == 'down'){
						this.$store.commit({
							type: 'showMenu',
							hide: false
						});
					} else {
						this.$store.commit({
							type: 'showMenu',
							hide: true
						});
					}
				}, 'end-=0.1');
			}
		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/settings';

.scene-hero {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: calc(100% + 63px);
	color: $color-white;

	.image-preload {
		opacity: 0;
		z-index: -2;
	}
	&__disclamer {
		position: absolute;
		bottom: rem(135 + 63); // 63 is the extra height for the sceneHeight, 100
		width: 60%;
		// background-color: rgba($color-nevada, .2);
		background-color: rgba(51, 51, 51, .3);
		border-radius: rem(3);
		padding: rem(30);
		color: $color-white;
		z-index: 10;
		left: 50%;
		transform: translate3d(-50%, 0, 0);
	}
	&__ie-shop-now {
		position: absolute;
		bottom: rem(50 + 63);
		left: 0;
		width: 100%;
		text-align: center;
		.button {
			display: inline-block;
			background-color: $color-white;

			@include mq($until: screen-sm){
				max-width: rem(300);
			}
		}
	}

	.headline-wrapper {
		position: absolute;
		left: 50%;
		top: 260px;
		width: 80%;

		@include mq($from: screen-xxxs){
			top: 280px;
		}
		@include mq($from: screen-xxs){
			top: 310px;
		}
		@include mq($from: screen-xs){
			top: 350px;
		}
		@include mq($from: screen-sm){
			top: 390px; // tablet
		}
		@include mq($from: screen-sm-plus){
			top: 410px;
		}
		@include mq($from: screen-md){
		// 	top: 350px;
		// }
		// @include mq($from: screen-lg){
			top: 45%;
		}
	}

	.video-click {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100vh;
		opacity: 1;
		// background-color: $color-carousel-pink;

		z-index: 20;
		// background-color: rgba(255, 0, 0, 0.5);

		.sound-icon {
			position: fixed;
			bottom: 7%;
			left: 50%;
			transform: translateX(-50%);
			width: rem(52);
			height: rem(52);
			background-color: rgba(51, 51, 51, 0.3); // #333
			// border-radius: rem(5);
			border-radius: 50%;

			opacity: 0;
			visibility: hidden;

			@include mq($from: screen-sm){
				width: rem(75);
				height: rem(75);
			}
			@include mq($from: screen-lg){
				width: rem(90);
				height: rem(90);
			}

			.sound-icon__svg {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				width: rem(25);
				height: rem(25);

				@include mq($from: screen-sm){
					width: rem(35);
					height: rem(35);
				}
				@include mq($from: screen-lg){
					width: rem(45);
					height: rem(45);
				}
			}
			.sound-icon__sound {
				display: none;
			}

			&.unmuted {
				.sound-icon__sound {
					display: block;
				}
				.sound-icon__muted {
					display: none;
				}
			}
		}
	}

	.scene-headline {
		opacity: 0;
		visibility: hidden;

		&.show {
			opacity: 1;
			visibility: visible;
		}

		h1,
		.scene-headline__manchet {
			position: absolute;
			opacity: 0;
			visibility: hidden;
		}
	}

	.scroll-indicator {
		.arrow-wrapper {
			position: fixed;
			bottom: rem(30);
			left: 50%;
			transform: translateX(-50%);

			width: rem(60);
			height: rem(60);

			@include mq($from: screen-md){
				width: rem(85);
				height: rem(85);
				bottom: rem(60);
			}
			@include mq($from: screen-xl){
				width: rem(120);
				height: rem(120);
				bottom: rem(80);
			}

			.arrow-circle {
				background-color: rgba(51, 51, 51, 0.3); // #333
				border-radius: 50%;

				width: 100%;
				height: 100%;

				opacity: 0;
				visibility: hidden;
			}

			// .arrow-down,
			.indicator-mouse,
			.gesture {
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);

				opacity: 0;
				visibility: hidden;

				@include mq($until: screen-md){
					width: rem(19);
					height: rem(28);
				}
				@include mq($from: screen-md, $until: screen-xl){
					width: rem(23);
					height: rem(33);
				}
				@include mq($from: screen-md){
					display: none;
				}
				// @include mq($from: screen-xl){
					// arrow original = 22 x 36px
					// gesture original = 27 x 39px
				// }
			}
			.indicator-mouse {
				@include mq($until: screen-md){
					width: rem(19);
					height: rem(28);
					display: none;
				}
				@include mq($from: screen-md, $until: screen-xl){
					width: rem(23);
					height: rem(33);
				}
				@include mq($from: screen-md){
					display: block;
					// opacity: 1;
					// visibility: visible;
				}
			}
		}
	}
}

</style>



