<template>
	<ul class="instagram-feed">
		<!-- <li v-for="(feed, index) in feeds">
			<InstagramFeedEntry :mediaId="feed" :token="token" />
		</li> -->
		<li v-for="(feed, index) in tempImages">
			<InstagramFeedEntry :mediaId="feed.image" :tmpUrl="feed.url" :token="token" :isChina="isChina" />
		</li>
	</ul>
</template>

<!--
	COMPONENT DESCRIPTION

	At the time of writing, the two flower-animations are set to autoplay in different ways, in order to test the most performant approach..

-->

<script>
import {MapState } from 'vuex';
import { Power4, TimelineMax } from 'gsap';

import InstagramFeedEntry from './InstagramFeedEntry';

export default {
	name: 'InstagramFeed',
	components: {
		InstagramFeedEntry,
	},
	props: {
		feed: {
			type: Array,
			required: true,
		},
		token: {
			type: String,
			required: true,
		},
		isChina: Boolean,
	},
	data: () => ({
		feeds: [],
		loadedImages: 0,
		tempImages: [
			// '//cdn.sorthvid.design/images/instagram/1.jpg',
			// '//cdn.sorthvid.design/images/instagram/2.jpg',
			// '//cdn.sorthvid.design/images/instagram/3.jpg',
			// '//cdn.sorthvid.design/images/instagram/4.jpg',
			// '//cdn.sorthvid.design/images/instagram/5.jpg',
			// '//cdn.sorthvid.design/images/instagram/6.jpg',
			// '//cdn.sorthvid.design/images/instagram/7.jpg',
			// '//cdn.sorthvid.design/images/instagram/8.jpg',
			{image: '//cdn.sorthvid.design/images/instagram/14_03_1.jpg', url: '//instagram.com/theofficialpandora'},
			{image: '//cdn.sorthvid.design/images/instagram/14_03_2.jpg', url: '//instagram.com/theofficialpandora'},
			{image: '//cdn.sorthvid.design/images/instagram/14_03_3.jpg', url: '//instagram.com/theofficialpandora'},
			{image: '//cdn.sorthvid.design/images/instagram/14_03_4.jpg', url: '//instagram.com/theofficialpandora'},
		]
	}),
	created(){
		var today = new Date();
		var the17th = new Date('March 17, 2019 00:00:01');
		var the18th = new Date('March 18, 2019 00:00:01');
		var the19th = new Date('March 19, 2019 00:00:01');
		var the21st = new Date('March 21, 2019 00:00:01');

		if(today > the17th){
			this.tempImages.push({image: '//cdn.sorthvid.design/images/instagram/17_03.jpg', url: '//instagram.com/theofficialpandora'});
		}
		if(today > the18th){
			this.tempImages.push({image: '//cdn.sorthvid.design/images/instagram/18_03.jpg', url: '//instagram.com/theofficialpandora'});
		}
		if(today > the19th){
			this.tempImages.push({image: '//cdn.sorthvid.design/images/instagram/19_03.jpg', url: '//instagram.com/theofficialpandora'});
		}
		if(today > the21st){
			this.tempImages.push({image: '//cdn.sorthvid.design/images/instagram/21_03.jpg', url: '//instagram.com/theofficialpandora'});
		}
	},
	mounted(){
		// let scrElement = document.createElement( 'script' );

		this.loadedImages = this.tempImages.length;

		// window.instagramProcessResult = function () {
		// 	this.feeds = this.feed;
		// }.bind(this);

		// scrElement.setAttribute( 'src', 'https://api.instagram.com/v1/users/self/media/recent?access_token=' + this.token + '&callback=instagramProcessResult' );
		// document.body.appendChild( scrElement );
	},
	methods: {
		imagesLoaded(){
			this.loadedImages--;

			if (this.loadedImages === 0) {
				this.$parent.instagramFeedLoaded();
			}
		}
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../assets/global';

// .instagram-feed {
// 	// width: 100%;

// 	// position: fixed;
// 	// top: 0;
// 	// left: 0;

// 	// z-index: 9999;

// 	// background-color: #fff;

// 	// li {
// 	// 	display: inline-block;
// 	// }
// }

.instagram-feed {
	padding: 0;
	padding-top: 30%; // move the top, as the items has -30%
	margin: 0;
	display: flex;
	flex-direction: column;

	li {
		display: block;

		width: 60%;

		@include mq($from: screen-md){
			width: auto;
		}

		&:nth-child(even) {
			align-self: flex-end;
		}

		@include mq($from: screen-md){
			&:nth-child(4) {
				position: relative;
				z-index: 10;	
			}

			&:nth-child(5) {
				position: relative;
				z-index: 1;	
			}

			&:nth-child(6) {
				position: relative;
				z-index: 0;	
			}
		}
	}
}
</style>



