<template>
	<section class="intro">
		<!-- <div v-if="showLoadIndicator" class="load-indicator" ref="loadIndicator"></div> -->
		<div class="bg bg--intro" ref="bg"></div>

		<SceneHeadline
			ref="headline"
			:headline="introTitle"
			:manchet="introManchet"
			class="headline-wrapper"
			:showIntroSpinner=true
			/>

		<!-- <div class="spinner loading"></div> -->

		<!-- <MqShow if="md" ref="FlowerOneLeaf1mq">
			<FlowerOneLeaf
				ref="FlowerOneLeaf1"
				id="intro-flower1"
				position="flower--top-right"
				width=400
				height=500
				/>
		</MqShow> -->
		<!-- Only FlowerBlossom has the animComplete, as it's a bit delayed and the last one to complete -->
		<FlowerBlossom
			ref="FlowerBlossom"
			id="flower-blossom"
			position="flower-blossom--intro"
			width=400
			height=500
			:startAtLastFrame=true
			v-on:animationCompleted="flowerAnimCompleted"
			v-on:animationLoaded="blossomLoaded"
			/>
		<FlowerTwoLeaves
			ref="FlowerTwoLeaves"
			id="flower-twoleaves"
			position="flower-twoleaves--intro"
			width=400
			height=500
			:startAtLastFrame=true
			v-on:animationLoaded="twoLeavesLoaded"
			/>

	</section>
</template>

<!--
	COMPONENT DESCRIPTION

	At the time of writing, the two flower-animations are set to autoplay in different ways, in order to test the most performant approach..

-->

<script>
import {mapState } from 'vuex';
// import ProgressMixin from '../mixins/ProgressMixin';

import SceneHeadline from '../SceneHeadline';
// import FlowerOneLeaf from '../animations/FlowerOneLeaf';
import FlowerBlossom from '../animations/FlowerBlossom';
import FlowerTwoLeaves from '../animations/FlowerTwoLeaves';

export default {
	name: 'Intro',
	// mixins: [ProgressMixin],
	components: {
		SceneHeadline,
		// FlowerOneLeaf,
		FlowerBlossom,
		FlowerTwoLeaves,
	},
	props: {
		options: null, // scene object from store state scenes
	},
	data(){
		return {
			mq: this.$store.state.mq,
			showLoadIndicator: true,
			introAnimationRan: false,
			flowerAnimCount: 0,
			flowerLoadedCount: 0,
		};
	},
	computed: {
		introTitle(){
			return this.$i18n.t('intro_title');
		}, 
		introManchet(){
			return this.$i18n.t('intro_manchet');
		}, 
	},
	created(){
		this.tl = new TimelineMax({ paused: false }); // headline intro animation (overwrite mixin default)
		// this.loadIndicatorTl = new TimelineMax({ paused: false });
		this.outroTl = new TimelineMax({ paused: true, autoRemoveChildren: false });
	},
	mounted(){
		if(this.$store.state.showIntro){ // test if intro should be shown (disable in dev mode)
			this.$nextTick(this.setupScene);
		} else {
			this.$el.classList.add('hidden');
			this.$el.classList.remove('active-pointer');
			this.$el.classList.add('inactive-pointer');
		}
	},

	methods: {
		setupScene(){
			this.outroTl
				.fromTo( // animate headline up and opacity zero
					this.$refs.headline.$el,
					2,
					{
						y: '-=0px',
						autoAlpha: 1,
						// ease: Linear.easeNone,
						ease: Power4.easeInOut,
					},
					{
						y: '-=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
						autoAlpha: 0,
						// ease: Linear.easeNone,
						ease: Power4.easeInOut,
					}
				)
				.add('outro-end')
				.progress(1, true) // cache animation, but don't do callbacks (the true parameter)
				.progress(0, true) // set stort pos back to 0
				// .pause()
				;

			// COMMENT: Safety-hide the intro headline, as it might get stuck/come back after animation is done
			this.outroTl.addCallback(()=>{
				this.$refs.headline.$el.classList.add('hidden');
			}, 'outro-end');


			if(this.$store.state.currentProgress.pixels === 0 && !this.introAnimationRan){ // initial page load
				this.headlineFadeIn();
				this.animateFlowers();
			} else {
				// position flowers and headline outro animation based on progress
				// this.outroTl
				// 	.to( // animate headline up and opacity zero
				// 		this.$refs.headline.$el,
				// 		0,
				// 		{
				// 			// y: '-=' + ((this.$store.state.windowHeight / 100) * this.$store.state.headlineAnimationDistance) + 'px',
				// 			opacity: 0,
				// 			ease: Linear.easeNone,
				// 		}
				// 	);
			}
		},
		blossomLoaded(){
			// console.log('intro blossom loaded');
			this.flowerLoadedCount++;
			this.allFlowersLoaded();
		},
		twoLeavesLoaded(){
			// console.log('intro twolaeves loaded');
			this.flowerLoadedCount++;
			this.allFlowersLoaded();
		},
		allFlowersLoaded(){
			if(this.flowerLoadedCount > 1){
				// console.log('intro - all flowers loaded');
				this.$emit('allFlowersLoaded');
			}
		},
		headlineFadeIn(){ // onload/after preload
			// this.tl.from(this.$refs.headline.$el, 1.5, { y: '40px', opacity: 0, ease: Power4.easeOut });
			this.tl
				.fromTo(this.$refs.headline.$el, 4,
					{ // COMMENT: Only fade first headline (intro)
						autoAlpha: 0,
						ease: Power2.easeOut
					},
					{
						autoAlpha: 1,
						ease: Power2.easeOut
					},
				);
		},
		flowerAnimCompleted(){
			this.flowerAnimCount++;
			this.introAnimationRan = true;
			// console.log('intro animations are done... ', this.flowerAnimCount);
			if(this.flowerAnimCount == 1){ // COMMENT: First roundtrip is flower animation IN done
			} else if(this.flowerAnimCount == 2){ // COMMENT: Second roundtrip is flower animation OUT done
				
				// COMMENT: Now we know the intros outro is done, then hide it
				this.$el.classList.add('hidden');
			}
		},
		runOutro(){
			this.outroTl.delay(0).play();
			this.$refs.FlowerTwoLeaves.setReverse();
			this.$refs.FlowerTwoLeaves.play();
			this.$refs.FlowerBlossom.setReverse();
			this.$refs.FlowerBlossom.play();
		},

		animateFlowers(){
			this.$refs.FlowerTwoLeaves.play(); // play from last frame and stops via "startAtLastFrame"
			this.$refs.FlowerBlossom.play(); // play from last frame and stops via "startAtLastFrame"
			// this.delayedSecondFlower();
		},
		// delayedSecondFlower(){
		// 	setTimeout(() => { // slight delay on second flower animation
		// 		this.$refs.FlowerBlossom.play();
		// 	}, 350);
		// },

		// COMMENT / SAVE : Progress flower animation based on scroll progress
		// progressFlowers(progress){ // can be used for butterfly's and all the other animations as well
		// 	// if(this.$refs.FlowerOneLeaf1mq.$mqAliases.md){
		// 	// 	var progressFlower1 = this.calcAnimationFrame(this.$refs.FlowerOneLeaf1, progress);
		// 	// 	this.$refs.FlowerOneLeaf1.jumpToFrame(progressFlower1);
		// 	// }
		// 	var progressFlower2 = this.calcAnimationFrame(this.$refs.FlowerTwoLeaves, progress);
		// 	this.$refs.FlowerTwoLeaves.jumpToFrame(progressFlower2);
		// 	// var progressFlower2 = this.$refs.FlowerTwoLeaves.totalFrames - (this.$refs.FlowerTwoLeaves.totalFrames / 100) * (progress * 100);

		// 	// console.log('------------------------------------------- progress flowers?: ', progress);
		// 	// console.log('------------------------------------------- progress flowers?: ', this.calculateSceneProgress());
		// 	// console.log('progress flowers?: ', this.$refs.FlowerTwoLeaves.totalFrames);

		// },

		// runIntro(progress){
		// 	// this.progressFlowers(progress);
		// 	// console.log('this.outroTl: ', this.outroTl);
		// 	// this.outroTl.tweenTo(progress);
		// 	if(this.introTl){
		// 		this.introTl.progress(progress);
		// 	}
		// },

		// runOutro(progress){
		// 	this.progressFlowers(progress);
		// 	// console.log('this.outroTl: ', this.outroTl);
		// 	// this.outroTl.tweenTo(progress);
		// 	this.outroTl.progress(progress);
		// },

		// replay(){ // SAVE: Nice to have around for other animations
		// 	// this.headlineFadeOut();
		// 	this.$refs.FlowerOneLeaf1.setForward();
		// 	this.$refs.FlowerOneLeaf1.changeSpeed(1);
		// 	this.$refs.FlowerOneLeaf1.play();

		// 	this.$refs.FlowerTwoLeaves.setForward();
		// 	this.$refs.FlowerTwoLeaves.changeSpeed(1);
		// 	this.$refs.FlowerTwoLeaves.play();
		// },
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/settings';

.intro {
	position: fixed;
	top: 0;
	left: 0;
	color: $color-text;
	width: 100%;
	height: 100vh;

	.headline-wrapper { // same styling as in SceneHero
		position: absolute;
		left: 50%;
		top: rem(160);
		width: 80%;

		@include mq($from: screen-xxxs){
			top: rem(190);
		}
		@include mq($from: screen-xxs){
			top: rem(310);
		}
		@include mq($from: screen-xs){
			top: rem(350);
		}
		@include mq($from: screen-sm){
			top: rem(390); // tablet
		}
		@include mq($from: screen-sm-plus){
			top: rem(410);
		}
		@include mq($from: screen-md){
		// 	top: 350px;
		// }
		// @include mq($from: screen-lg){
			top: 41%;
		}
	}

	.bg--intro {
		z-index: 0;
	}

	h1 {
		color: $color-text;
	}
	.manchet {
		color: $color-text;
	}
	.scene-headline {
		z-index: 3;
	}
	.load-indicator {
		position: fixed;
		top: 0;
		left: 0;
		width: 0px;
		height: rem(3);
		background-color: $color-white;

		z-index: 3;

		@include mq($from: screen-md){
			height: rem(4);
		}
	}
}
</style>



