// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import VueI18n from 'vue-i18n';
import App from './App';

import store from './store/store';
import { languages, defaultLocale } from './locale';
Vue.config.productionTip = false;


fetch('//cdn.sorthvid.design/sprite.svg')
  .then(response => response.text())
  .then(svg => document.body.insertAdjacentHTML('afterbegin', svg));


const messages = Object.assign(languages);
let defaultLoc = '';
if(window.utag_data && window.utag_data.locale){
	defaultLoc = window.utag_data.locale;
} else if(window.utag_data && window.utag_data.site_locale){
	defaultLoc = window.utag_data.site_locale.replace('-', '_');
} else {
	defaultLoc = document.querySelector('html').getAttribute('lang') ? document.querySelector('html').getAttribute('lang').replace('-', '_') : defaultLocale;
}


