// Create VueI18n instance with options
Vue.use(VueI18n);
const i18n = new VueI18n({
	locale: defaultLoc,
	messages, // set locale messages
	fallbackLocale: 'en_US',
	silentTranslationWarn: true
});

/* eslint-disable no-new */
new Vue({
	el: '#pandora-garden',
	i18n,
	store,
	components: { App },
	template: '<App/>',
});



