<template>
	<div :style="style" ref="lavContainer"></div>
</template>

<script>
import Lottie from 'lottie-web';

export default {
	name: 'LottieBase',
	props: {
		id: String,
		options: {
			type: Object,
			required: true,
			loop: false,
			autoplay: false,
			animationData: null,
			rendererSettings: {},
		},
		width: Number,
		height: Number,
		hidden: {
			type: Boolean,
			default: true,
		},
	},
	data() {
		return {
			style: {
				width: this.width ? `${this.width}px` : '100%',
				height: this.height ? `${this.height}px` : '100%',
				overflow: 'hidden',
				margin: '0 auto',

				visibility: 'hidden',
				opacity: 0,

				// display: 'none',
			},
			anim: null, // will be lottie reference
		};
	},
	mounted() {
		//this.anim = Lottie;
		this.anim = Lottie.loadAnimation({
			container: this.$refs.lavContainer,
			// renderer: 'svg',
			renderer: 'canvas',
			loop: this.options.loop !== false,
			autoplay: this.options.autoplay !== false,
			animationData: this.options.animationData,
			rendererSettings: this.options.rendererSettings,
		});
		this.$emit('animCreated', this.anim);
		this.anim.onComplete = () => {
			/* eslint-disable no-alert, no-console */
			// console.log('Anim done - id: ', this.id);
			/* eslint-enable no-alert, no-console */
			this.$emit('animCompleted', this.anim);
		};
		this.anim.addEventListener('DOMLoaded', function(e) {
			// console.log('Lottie loaded');
			this.$emit('animLoaded', this.anim);
		}.bind(this));
	},
	methods: {
		show(){
			// this.$refs.lavContainer.style.display = 'block';
			this.$refs.lavContainer.style.visibility = 'visible';
			this.$refs.lavContainer.style.opacity = 1;
		},
		hide(){
			// this.$refs.lavContainer.style.display = 'none';
			this.$refs.lavContainer.style.visibility = 'hidden';
			this.$refs.lavContainer.style.opacity = 0;
		},
	// 	getTotalFrames(){
	// 		return this.anim.getDuration(true); // If true, returns duration in frames, if false, in seconds.
	// 	},
	},
};
</script>



