			<img :src="`${staticRoot}images/ladybird/ladybird-trim.png`" alt="">
		</div>
		<div class="text" ref="ladybirdText">
			{{ $t('ladybird_content_headline') }}
			<!-- <div class="skinny">{{ $t('pandoragarden_hashtag') }}</div> -->
			<div v-if="isChina === false" class="skinny">{{ $t('pandoragarden_hashtag') }}<br>@theofficialpandora</div>
			<a v-if="isChina === false" href="//instagram.com/theofficialpandora" target="_blank" class="insta-circle" title="View Pandora on Instagram">
				<svg class="insta-icon" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 14 14"><g fill="#fff" fill-rule="evenodd"><path d="M6.98897155.0220569c-1.89808752 0-2.13608752.00799562-2.8815186.04206126-.74392997.03397374-1.25197374.15210066-1.6964814.32484902-.4596105.17859956-.84937418.41757987-1.23791247.80611816-.3885383.3885383-.6275186.7783326-.80608753 1.23791247-.17284026.44456894-.29096717.9526127-.32487965 1.69651205-.034035.74543107-.04206127.98340043-.04206127 2.88148796 0 1.8981488.00802626 2.13611817.04206127 2.88154925.03391248.74389935.1520394 1.25194315.32487965 1.69651205.17856893.4595798.41754924.8493742.80608753 1.2379125.3885383.3885382.77830197.6275186 1.23791247.8061181.44450766.1727484.95255143.2908753 1.6964814.324849C4.85288403 13.9919431 5.09088403 14 6.98897155 14c1.89808753 0 2.13611817-.0080569 2.88154924-.0420613.7438993-.0339737 1.2519431-.1521006 1.6964814-.324849.4596105-.1785995.8493435-.4175799 1.2378818-.8061181.3885383-.3885383.6275186-.7783327.8061182-1.2379125.1728096-.4445689.2909365-.9526127.3248796-1.69651205.034035-.74543108.0420307-.98340044.0420307-2.88154924 0-1.89808752-.0079957-2.13605688-.0420307-2.88148795-.0339431-.74389935-.15207-1.2519431-.3248796-1.69651204-.1785996-.45957986-.4175799-.84937417-.8061182-1.23791246-.3885383-.3885383-.7782713-.6275186-1.2378818-.80611816-.4445383-.17274836-.9525821-.29087528-1.6964814-.32484902C9.1250897.03005252 8.88705907.0220569 6.98897154.0220569m0 1.25926476c1.86613567 0 2.08713348.00710722 2.82414005.04071335.6814048.0310941 1.0514705.14499345 1.2977418.24066522.3262582.1267965.559081.27825383.8036061.52280963.2446171.24458643.3960744.47737855.5228096.80360612.0956718.24627134.2095712.61636762.2406653 1.2977418.0336061.73700656.0407439.95800437.0407439 2.82414004 0 1.86619695-.0071378 2.08719476-.0407439 2.82414006-.0310941.68143544-.1449935 1.05153174-.2406653 1.29780304-.1267352.326197-.2781925.5590197-.5228096.8036061-.2445251.2445558-.4773479.3960132-.8036061.522779-.246271