<script>
import {mapState } from 'vuex';
import ProgressMixin from '../mixins/ProgressMixin';

import SceneHeadline from '../SceneHeadline';
import InstagramFeed from '../InstagramFeed';
import LadybirdSmallAnimation from '../animations/LadybirdSmall';
import FlowerTwoLeaves from '../animations/FlowerTwoLeaves';

export default {
	name: 'SceneContentLadybird',
	mixins: [ProgressMixin],
	components: {
		SceneHeadline,
		InstagramFeed,
		LadybirdSmallAnimation,
		FlowerTwoLeaves,
	},
	props: {
		options: Object,
		className: String,
	},
	// mounted(){
		// console.log('mounted - this.options.name: ', this.options.name);
		// this.$nextTick(this.setupScene);
	// },
	data() {
		return {
			isChina: this.$i18n.locale === 'zh_CN',
			staticRoot: this.$store.state.staticRoot,
			mq: this.$store.state.mq,
			instagramFeed: this.$store.state.instagram.feed,
			instagramToken: this.$store.state.instagram.token,
		};
	},
	methods: {
		ladybirdAnimationCompleted() {
			// console.log('ladybird animation completed');
		},
		instagramFeedLoaded() {
			this.$nextTick(this.setupScene);
		},
		setupScene(){
			// console.log('ladybird - isChina: ', this.isChina);
			var bg = this.$refs.bg,
				feed = this.$refs.feed,
				feedChildren = this.$refs.feed.getElementsByTagName('li'),
				ladybird = this.$refs.ladybirdFixed,
				text = this.$refs.ladybirdText,
				feedHeight = feed.clientHeight,
				tlProgress = 0,
				scrollDirection = 'down';


			this.introTl = new TimelineMax({paused: true, onUpdateParams: ['{self}'], onUpdate: function(introTl) {
				var currentTlProgress = introTl.progress();
				if(currentTlProgress > tlProgress){
					scrollDirection = 'down'
				} else {
					scrollDirection = 'up'
				}
				tlProgress = introTl.progress();
			}});

			var wh = this.$store.state.windowHeight;

			// this.introTl
			// 	.set(bg, {autoAlpha: 0});

			this.ladybirdFixedTl = new TimelineMax();
			this.ladybirdFixedTl
				.to(ladybird, 0, { autoAlpha: 0 })
				.fromTo(ladybird, 2,
					{
						y: '+=' + (wh) + 'px', // wh plus 100 px
						autoAlpha: 1,
					},
					{
						y: '+=10%',
						autoAlpha: 1,
						ease: Sine.easeOut,
					},
					// 'intro-start', // label is in play when used in introTl
				)
				.fromTo(ladybird, 3,
					{
						y: '+=0%',
						autoAlpha: 1,
					},
					{
						y: '-=20%',
						autoAlpha: 1,
						ease: Linear.easeNone,
					},
					// 'intro-start+=1', // label is in play when used in introTl
				)
				.fromTo(ladybird, 1.3,
					{
						y: '+=0%',
						autoAlpha: 1,
					},
					{
						y: '-=5%',
						autoAlpha: 0,
						ease: Linear.easeNone,
					},
					// 'intro-start+=1', // label is in play when used in introTl
				)
				.to(ladybird, 0, { autoAlpha: 0 })
				;

			// console.log('ladybird text: ', text); // for easy access in devTools
			this.ladybirdTextTl = new TimelineMax();
			this.ladybirdTextTl
				.to(text, 0, { autoAlpha: 0 })
				.fromTo(text, 5,
					{
						y: '+' + (wh) + 'px',
						autoAlpha: 1,
					},
					{
						y: '+' + (wh / 4) + 'px',
						autoAlpha: 1,
						ease: Power2.easeOut,
					},
					// 'content-start', // label is in play when used in introTl
				)
				.fromTo(text, 2,
					{
						y: '+' + (wh / 4) + 'px',
						autoAlpha: 1,
					},
					{
						y: '-=100%',
						autoAlpha: 1,
						ease: Power2.easeIn,
					},
					// 'content-start+=3', // label is in play when used in introTl
				)
				.to(text, 0, { autoAlpha: 0 })
				;

			this.introTl
				.add('intro-start')
				.to(feed, 0, {
					autoAlpha: 1,
				})
				.fromTo(bg, 1,
					{
						autoAlpha: 0,
						ease: Linear.easeNone,
					},
					{
						autoAlpha: 1,
						ease: Linear.easeNone,
					}
				)
				.add('content-start')

				.addLabel('ladybird1-intro', 'content-start+=0.3') // timing cannot be 0, as it doesn't provoke the direction
				.addLabel('ladybird2-intro', 'content-start+=0.5')
				.addLabel('twoleaves-intro', 'content-start+=0.5')
				// .addLabel('ladybird3-intro', 'content-start-=0.1')

				.add(this.ladybirdFixedTl, 'intro-start+=0.6', 'ladybirdFixed')
				.add(this.ladybirdTextTl, 'intro-start+=0.2', 'ladybirdText')

				.staggerFromTo(feedChildren, 7,
					{
						y: '+' + this.$store.state.windowHeight + 'px',
						autoAlpha: 1,
					},
					{
						y: '-' + 0 - (feedHeight * 1.1) + 'px',
						autoAlpha: 1,
						ease: Power2.easeInOut,
					}, 
					.05, 
					'intro-start+=0.8',
				)

				.add('content-outro')				
				.addLabel('ladybird1-outro', 'content-outro-=2.8') // timing cannot be 0, as it doesn't provoke the direction
				.addLabel('ladybird2-outro', 'content-outro-=2.6')
				.addLabel('twoleaves-outro', 'content-outro-=2.6')
				// .addLabel('ladybird3-outro', 'content-outro-=0.2')

				.add('content-end')
				.fromTo(bg, 0.6,
					{
						autoAlpha: 1,
						delay: -1.2,
					},
					{
						autoAlpha: 0,
						ease: Linear.easeNone,
						delay: -1.2,
					}
				)
				.add('end')
				.progress(1, true) // cache animation, but don't do callbacks (the true parameter is suppressing events)
				.progress(0, true)
				;

			if(this.mq.md){ // COMMENT: media query check, same as on the animation-components in the template
				var ladybirdAnimations = [ // corresponds to the same animation-components in the tempalte above
					{
						el: this.$refs.ladybird1,
						introLabel: 'ladybird1-intro',
						outroLabel: 'ladybird1-outro',
					},
					// {
					// 	el: this.$refs.ladybird2,
					// 	introLabel: 'ladybird2-intro',
					// 	outroLabel: 'ladybird2-outro',
					// },
					{
						el: this.$refs.FlowerTwoLeaves,
						introLabel: 'twoleaves-intro',
						outroLabel: 'twoleaves-outro',
					},
					// {
					// 	el: this.$refs.ladybird3,
					// 	introLabel: 'ladybird3-intro',
					// 	outroLabel: 'ladybird3-outro',
					// },
				];

				ladybirdAnimations.forEach(function(ladybird, i){
					// trigger ladybird intro animations based on gsap introTl timeline labels
					this.introTl.addCallback(() => {
						// console.log('ladybird this: ', this);
						if(scrollDirection == 'down'){
							ladybird.el.setForward();
							ladybird.el.play();
						} else {
							ladybird.el.playOutro();
						}
					}, ladybird.introLabel);

					// // trigger ladybird outro animations based on gsap introTl timeline labels
					this.introTl.addCallback(() => {
						// console.log('ladybird this: ', this);
						if(scrollDirection == 'down'){
							ladybird.el.playOutro();
						} else {
							ladybird.el.setForward();
							ladybird.el.play();
						}
					}, ladybird.outroLabel);
				}.bind(this));
			}
		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/settings';

