	// 	pointer-events: none;

	// 	@include mq($from: screen-md){
	// 		left: 49%;
	// 		width: 50%;
	// 	}

	// }

	.insta-circle {
		position: absolute;
		bottom: rem(-65); // unconventional margin-top 15
		left: 0;
