		left: 10%;
		width: 40%;
		z-index: 7;
	
		font-family: $font-stack-bold;
		// font-size: rem(50);
		font-size: rem($title-1-min);
		text-align: left;
		line-height: 1.1;

		max-width: rem(350);

		opacity: 0;
		visibility: hidden;

		@include mq($from: screen-md){
			left: 50%;
