	.ladybird-fixed { // the ladybird image
		// display: none;
		position: absolute;
		top: 0%;
		right: -25%;
		// float: right;
		width: 100%;
		// max-height: rem(1200);
		// left: -3%;

		text-align: right;

		z-index: 0;

		opacity: 0;
