<template>
	<footer class="scene-content" :class="[className, inView ? 'active-pointer' : '']" ref="container">
		<div class="bg" ref="bg"></div>
		<div class="content" ref="content">
			<div class="content__inner">
				<h2>{{ $t('endscene_headline') }}</h2>

				<picture ref="picture" class="poster-image" style="visibility: inherit; opacity: 1;">
					<source :srcset="`${staticRoot}images/footer-bg-sm.jpg`" media="(max-width: 768px)">
					<source :srcset="`${staticRoot}images/footer-bg-lg.jpg`" media="(min-width: 769px)">
					<img :src="`${staticRoot}images/footer-bg-lg.jpg`">
				</picture>

				<div class="button-wrapper">
					<a href="javascript:;" class="button" @click="trackCollectionClick" target="_blank">
						{{ $t('endscene_cta') }}
					</a>
				</div>
			</div>
			
		</div>
	</footer>
</template>

<script>
import { MapState, mapActions } from 'vuex';
import ProgressMixin from '../mixins/ProgressMixin';

import SceneHeadline from '../SceneHeadline';

export default {
	name: 'Footer',
	mixins: [ProgressMixin],
	components: {
		SceneHeadline,
	},
	props: {
		options: Object,
		// className: String,
	},
	mounted(){
		// console.log('mounted - this.options.name: ', this.options.name);
		this.$nextTick(this.setupScene);
		// console.log('this.className: ', this.className);
	},
	data() {
	  return {
	  		// startClassName: this.className,
	  		className: 'scene--footer',
			staticRoot: this.$store.state.staticRoot,
	  };
	},
	methods: {
		...mapActions([
			'registerShopClick',
		]),
		trackCollectionClick() {
			if(this.$store.state.debugTracking){
				console.log('Tracking - Registered shop collection click.');
			}
			if(window.utag){
				// COMMENT: Gather all tracking info
				var allSkuNumbers = [],
					allProductNames = [],
					allProductPrices = [],
					allProductCategories = [],
					allProductColors = [],
					allProductUrls = [],
					allProductDescs = [],
					allProductQuantities = [];

				// console.log('locale: ', this.$i18n.locale);
				this.$store.state.products.forEach(product => {
					if(product.hideInMenu === false){

						var sku = product.sku,
							name = this.$t(`${product.sku}_name`),
							desc = this.$t(`${product.sku}_description`),
							price = this.$t(`${product.sku}_price`).replace(/[^\d\.\,]/g, '');

						if(this.$store.state.debugTracking){
							console.log('menu collec - price: ', price);
							console.log('name: ', name);
							console.log('desc: ', desc);
						}

						allSkuNumbers.push(sku);
						allProductNames.push(name);
						allProductPrices.push(price);
						allProductDescs.push(desc);

						allProductQuantities.push('1');
						allProductCategories.push('');
						allProductUrls.push('');
						allProductColors.push('');
					}
				});

				window.utag.link({
					// eCapacity comment: product data of all involved products  see this section for further instructions
					product_sku: allSkuNumbers,
					product_id: allSkuNumbers,
					product_name: allProductNames,
					product_price: allProductPrices,
					product_unit_price: allProductPrices,
					product_quantity: allProductQuantities,

					// Empty but necessary
					product_category: allProductCategories,
					product_color: allProductColors,
					product_url: allProductUrls, // (S/H add place holder and generate url based on same URL that shop now points to)
					product_image_url: allProductUrls,

					estore_event: ['Pandora Garden shop the collection'],
				});
			}
			window.open(this.$t('endscene_cta_link'), '_blank');
		},
		setupScene(){
			// const bg = document.querySelector('.bg--main');
			const bg = this.$refs.bg;
			const content = this.$refs.content;
			const picture = this.$refs.picture;

			this.introTl
				.set(bg, { autoAlpha: 0 });

			this.introTl
				.add('intro-start')
				.to(bg, 1, {
					autoAlpha: 1,
					ease: Linear.easeNone,
				})
				.add('content-start')
				.fromTo(content, 1,
					{
						y: '+=50px',
						autoAlpha: 0,
						// ease: Power4.easeIn
					},
					{
						y: '+=0px',
						autoAlpha: 1,
						ease: Power4.easeIn
					},
					'-=0.5',
				)

				.from(picture, 1,
					{
						y: '+=20%',
					},
					// {
					// 	y: '-=50%',
					// },
					'-=0.7',
				)

				.add('content-end')
				.add('end')
				.progress(1, true)
				.progress(0, true)
				;

			this.introTl.addCallback(()=>{
				if(this.$store.state.debugTracking){
					console.log('Tracking - Registered footer. Sending utag link, final content.');
				}
				if(window.utag){
					window.utag.link({
						estore_event: ['Pandora Garden final content']
					});
				}
			}, 'content-end');

			this.introTl.addCallback(()=>{
				// console.log('intro start callback remove class');
				// this.$refs.container.classList.remove('active-pointers');
				this.className = 'scene--footer';
			}, 'intro-start');
			this.introTl.addCallback(()=>{
				this.className = 'scene--footer active-pointers'; // HARD FIXING ADD CLASS ISSUES
				// console.log('content start callback add class');
				// this.$refs.container.classList.add('active-pointers');
			}, 'content-start');
		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/settings';

.scene-content {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	min-height: 100vh;
	color: $color-white;
	// background-color: $color-carousel-pink;
	opacity: 1;

	text-align: center;
	// z-index: 1000000000000000;

	// pointer-events: auto;

	&.active-pointers {
		pointer-events: auto;
	}

	.bg {
		z-index: 0; // 
	}

	.content {
		position: relative;
		width: 100%;
		height: 100vh;
		margin: 0 auto;

		opacity: 0;
		// visibility: visible;
		visibility: hidden;

		&__inner {
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			
			width: 100%;
			height: 90%;
			
			padding-top: 0%;

			@include mq($from: screen-md){
				padding-top: 0;
				// height: 100vh;
			}

		}
	}

	h2 {
		margin-bottom: rem(10);
		margin-top: rem(10);
		text-align: center;
		font-size: rem(30);
		max-width: rem(600);
		
		opacity: 1;
		visibility: visible;
		// visibility: hidden;

		// transform: translateY(260%);

		@include mq($from: screen-sm){
			margin: 0 auto rem(0);
			font-size: rem(34);

			transform: translateY(320%);
		}
		@include mq($from: screen-md){
			margin: 0 auto rem(100);
			font-size: rem(38);
			transform: translateY(0);
		}

		@include mq($from: screen-lg){
			font-size: rem(44);
		}

		@include mq($from: screen-xl){
			font-size: rem(60);
		}
	}

	.poster-image {
		display: block;
		position: absolute;
		// top: 50%;
		top: rem(30);
		// transform: translateY(-50%);
		left: 0;
		width: 100%;
		// height: 100%;
		
		z-index: -1;

		text-align: left;

		@include mq($from: screen-sm){
			top: rem(60);
		}

		@include mq($from: screen-md){
			max-width: rem(1800);
			top: 26%;
		}

		img {
			width: 79%;
			height: auto;

			@include mq($from: screen-sm){
				width: 90%;
			}

			@include mq($from: screen-md){
				width: 84%;
			}
		}
	}

	.button-wrapper {
		position: absolute;
		bottom: rem(55);
		left: 0;
		width: 100%;
		text-align: center;
		z-index: 9;

		@include mq($from: screen-xs){
			bottom: rem(115);
		}
		@include mq($from: screen-md){
			bottom: rem(70);
		}
	}
	.button {
		position: relative;
		z-index: 9;
		// left: 50%;
		// transform: translateX(-50%);
		width: 76%;
		padding: rem(20) rem(20);
		// margin-top: rem(15);

		display: inline-block;

		background-color: $color-white;

		// transform: translateY(-260%);

		@include mq($from: screen-sm){
			width: auto;
			margin-top: 0;
			padding: rem(20) rem(70);
			// max-width: rem(450);
			// margin: auto;

			// transform: translateY(-120%);
		}

		@include mq($from: screen-lg){
			// transform: translateY(-120%);
		}
	}
}
// .button {
// 	border: rem(1) solid $color-mine-shaft;
// 	box-shadow: rem(10) rem(10) rem(40) rgba(0, 0, 0, .1);
// 	color: $color-mine-shaft;
// 	font-family: $font-stack;
// 	font-size: rem(20);
// 	letter-spacing: rem(3);
// 	padding: rem(20) rem(70);
// 	background: $color-white;
// 	display: block;
// 	text-transform: uppercase;
// 	text-decoration: none;
// 	text-align: center;
// }
</style>



