<template>
	<div class="scene-headline">
		<div v-if="showIntroSpinner" ref="introSpinner" class="spinner intro-spinner loading"></div>
		<h1 v-if="headline && firstHero" ref="headline">{{ $t(headline) }}</h1>
		<h2 v-if="headline && !firstHero" ref="headline">{{ $t(headline) }}</h2>
		<div class="scene-headline__manchet" v-if="manchet" ref="manchet">
			{{ $t(manchet) }}
		</div>
	</div>
</template>

<script>
export default {
	name: 'SceneHeadline',
	props: {
		headline: {
			type: [Boolean, String], // false for Branded-video component without title
		},
		manchet: {
			type: [Boolean, String],
			default: false,
		},
		firstHero: {
			type: Boolean,
			default: false
		},
		showIntroSpinner: {
			type: Boolean,
			default: false,
		},
	},
};
</script>

<style lang="scss" scoped>
@import '../assets/settings';

.scene-headline {
	position: absolute;
	top: 0;
	left: 50%;
	transform: translate(-50%, 0%);
	z-index: 2;
	max-width: rem(900);

	font-family: $font-stack;

	&__manchet {
		margin-top: rem(12);
		font-size: rem($manchet-min);

		@include mq($from: screen-md){
			font-size: rem($manchet-mid);
		}
		@include mq($from: screen-lg){
			font-size: rem($manchet-max);
		}
	}
}
h1,
h2 {
	font-size: rem($display-1-1);
	font-family: $font-stack;
	font-weight: normal;
	text-align: center;

	// @include mq($from: screen-sm){
	// 	font-size: rem(42);
	// }
	@include mq($from: screen-xs){
		font-size: rem($display-1-2);
	}
	@include mq($from: screen-sm){
		font-size: rem($display-1-3);
	}
	@include mq($from: screen-md){
		font-size: rem($display-1-4);
	}
	@include mq($from: screen-xl){
		font-size: rem($display-1-5);
	}
}
.intro-spinner {
	margin-bottom: rem(10);
	font-size: rem(30);
	border-width: rem(2);

	@include mq($from: screen-sm){
		font-size: rem(38);
		border-width: rem(2);
		margin-bottom: rem(30);
	}
	@include mq($from: screen-md){
		font-size: rem(44);
		border-width: rem(3);
		margin-bottom: rem(40);
	}
}
</style>



