<template>
	<header class="site-header" :class="[isHidden ? 'is-hidden' : '']">
		<div class="site-header__inner">
			<h1 class="site-header__logo">
				<a href="javascript:;" @click.prevent="gotoGlobalPandora">
					<svg viewBox="0 0 491.347 99.52" class="icon">
						<use xlink:href="#logo"></use>
					</svg>
					<span class="visually-hidden">{{ $t('general_title') }}</span>
				</a>
			</h1>

			<nav class="site-header__language-nav" :class="[showLanguageNav ? 'is-active' : '']">
				<button type="button" v-on:click="handleLanguageMenu">
					<span class="site-header__language-nav__flag">
						<!-- <img :src="selectedCountryIcon" :alt="selectedCountry"> -->
						<svg viewBox="0 0 100 100" class="icon">
							<use :xlink:href="`#${selectedCountryIcon}`"></use>
						</svg>
					</span>

					<span class="close-modal"></span>
				</button>

				<div ref="laguageCon" class="slide-in-modal slide-in-modal--auto-width slide-in-modal--from-left" :class="[showLanguageNav ? 'is-active' : '']">
					<div class="slide-in-modal__inner">
						<ul>
							<li v-for="country in countries" :key="country.countryCode">
								<a href="javascript:;" v-on:click="onCountryChange(country.countryCode, $event)">
									<span class="site-header__language-nav__flag">
										<!-- <img :src="country.icon" :alt="country.name"> -->
										<svg viewBox="0 0 100 100" class="icon" :data-cc="country.countryCode">
											<use :xlink:href="`#${country.countryCode.slice(3,5).toLowerCase()}`"></use>
										</svg>
									</span>{{ country.name }}</a>
							</li>
						</ul>
					</div>

					<span class="slide-in-modal__outside-click" v-on:click="handleLanguageMenu"></span>
				</div>
			</nav>

			<nav class="site-header__products" :class="[showProducts ? 'is-active' : '']">
				<button type="button" class="site-header__products__trigger" v-on:click="handleProductsMenu">
					<span class="site-header__products__trigger-text">{{ $t('menu_cta_text') }}</span>

					<span class="site-header__products__trigger-dots">
						<span></span><span></span><span></span>
					</span>

					<span class="close-modal"></span>
				</button>

				<div ref="productsCon" class="slide-in-modal" :class="[showProducts ? 'is-active' : '']">
					<div class="slide-in-modal__inner text--center">
						<h2 class="slide-in-modal__headline">{{ $t('menu_headline') }}</h2>
						<p class="slide-in-modal__text">{{ $t('menu_manchet') }}</p>

						<ul class="products">
							<li v-for="product in products" :key="product.sku">
								<button type="button" class="custom-cursor" @click="openClosedModal({sku: product.sku, isCard: false, target: $event.target})">
									<img :src="product.images.packshot">
								</button>
							</li>
						</ul>

						<a href="javascript:;" @click="trackCollectionClick" class="button" target="_blank">{{ $t('endscene_cta') }}</a>
					</div>

					<span class="slide-in-modal__outside-click" v-on:click="handleProductsMenu"></span>
				</div>
			</nav>
		</div>
		<div class="site-borders">
			<span class="site-borders__element site-borders__element--left"></span>
			<span class="site-borders__element site-borders__element--bottom"></span>
			<span class="site-borders__element site-borders__element--right"></span>
		</div>
	</header>
</template>

<!--
	COMPONENT DESCRIPTION

	At the time of writing, the two flower-animations are set to autoplay in different ways, in order to test the most performant approach..

-->

<script>
import {mapActions } from 'vuex';

// import { TweenMax, TimelineMax } from 'gsap';
import { Power4, TimelineMax } from 'gsap';

import Utils from './mixins/Utils';

export default {
	name: 'SiteHeader',
	// components: {
	// },
	data() {
		return {
			staticRoot: this.$store.state.staticRoot,
			mq: this.$store.state.mq,
			showProducts: false,
			showLanguageNav: false,
			countries: this.$store.state.countries,
			selectedCountry: this.$i18n.locale,
			languageMenuTl: null,
			productsMenuTl: null,
		};
	},
	computed: {
		isHidden() {
			this.handleIsHidden();
			return this.$store.state.hideSiteHeader;
		},
		selectedCountryIcon(){
			return this.$i18n.locale.slice(3,5).toLowerCase();
		},
		products() {
			return this.$store.state.products.filter(product => !product.hideInMenu)
				.sort( (a,b)=>a.menuOrder - b.menuOrder );
		}
	},
	mounted(){
		this.$nextTick(this.setupAnimations);
	},
	methods: {
		...mapActions([
			'openClosedModal',
			// 'registerShopClick'
		]),
		trackCollectionClick() {
			if(this.$store.state.debugTracking){
				console.log('Tracking - Registered shop collection click.');
			}
			if(window.utag){
				// COMMENT: Gather all tracking info
				var allSkuNumbers = [],
					allProductNames = [],
					allProductPrices = [],
					allProductCategories = [],
					allProductColors = [],
					allProductUrls = [],
					allProductDescs = [],
					allProductQuantities = [];

				// console.log('locale: ', this.$i18n.locale);
				this.$store.state.products.forEach(product => {
					if(product.hideInMenu === false){

						var sku = product.sku,
							name = this.$t(`${product.sku}_name`),
							desc = this.$t(`${product.sku}_description`),
							price = this.$t(`${product.sku}_price`).replace(/[^\d\.\,]/g, '');

						if(this.$store.state.debugTracking){
							console.log('menu collec - price: ', price);
							console.log('name: ', name);
							console.log('desc: ', desc);
						}

						allSkuNumbers.push(sku);
						allProductNames.push(name);
						allProductPrices.push(price);
						allProductDescs.push(desc);

						allProductQuantities.push('1');
						allProductCategories.push('');
						allProductUrls.push('');
						allProductColors.push('');
					}
				});

				window.utag.link({
					// eCapacity comment: product data of all involved products  see this section for further instructions
					product_sku: allSkuNumbers,
					product_id: allSkuNumbers,
					product_name: allProductNames,
					product_price: allProductPrices,
					product_unit_price: allProductPrices,
					product_quantity: allProductQuantities,

					// Empty but necessary
					product_category: allProductCategories,
					product_color: allProductColors,
					product_url: allProductUrls, // (S/H add place holder and generate url based on same URL that shop now points to)
					product_image_url: allProductUrls,

					estore_event: ['Pandora Garden shop the collection'],
				});
			}
			window.open(this.$t('endscene_cta_link'), '_blank');

		},
		handleIsHidden(){
			if(this.$store.state.hideSiteHeader){
				if(this.showProducts){
					this.productsMenuTl.seek('50').play();
					this.showProducts = false;
				}
				if(this.showLanguageNav){
					this.showLanguageNav = false;
					this.languageMenuTl.seek('50').play();
				}
			}
		},
		setupAnimations(){
			this.languageMenuTl = new TimelineMax({ paused: true });

			var languageCon = this.$refs.laguageCon;
			var langItems = languageCon.querySelectorAll('li');
			
			this.languageMenuTl
				.add('start')
				.to(languageCon, 2,
					{
						x: '0%',
						ease: Power4.easeOut,
					}
				)
				.staggerFrom(langItems, 1,
					{
						x: '-=20px',
						autoAlpha: 0,
						ease: Power4.easeOut,
					},
					'.1',
					'start+=0.5'
				)
				.addPause()
				.add('50')
				.staggerTo(langItems, .5,
					{
						x: '-=20px',
						autoAlpha: 0,
						ease: Power4.easeOut,
					},
					'-.1',
					// 'start+=0.5'
				)
				.to(languageCon, 1,
					{
						x: '-=100%',
						ease: Power4.easeInOut,
					},
					'50+=0.5',
				)
				;
			this.languageMenuTl.timeScale(2);

			var productsCon = this.$refs.productsCon;
			var productStaggerItems = [
				productsCon.querySelector('h2'),
				productsCon.querySelector('p.slide-in-modal__text'),
				productsCon.querySelector('ul'),
				// productsCon.querySelector('a.button'),
			];
			// var productStaggerItems = productsCon.querySelectorAll('li');
			this.productsMenuTl = new TimelineMax({ paused: true });
			this.productsMenuTl
				.add('start')
				.to(productsCon, 2,
					{
						x: '-=0%',
						ease: Power4.easeOut,
					}
				)
				.staggerFrom(productStaggerItems, 1,
					{
						y: '+=30px',
						autoAlpha: 0,
						ease: Power4.easeOut,
					},
					'.1',
					'start+=0.5'
				)
				.addPause()
				.add('50')
				.to(productsCon, 1,
					{
						x: '+=102%',
						ease: Power4.easeInOut,
					},
					// '50+=.4'
				)
				.staggerTo(productStaggerItems, 1,
					{
						y: '+=30px',
						autoAlpha: 0,
						ease: Power4.easeOut,
					},
					'-.1',
					'50+=0.3'
				)
				// .progress(1) // ROEN COMMENT: do not cache this, as the start-position of animation will be wrong..
				// .progress(0)
				;
		},
		handleLanguageMenu(){
			this.showLanguageNav = !this.showLanguageNav;
			if(this.showProducts === true){
				this.showProducts = false;
				this.productsMenuTl.seek('50').play();
			}

			if(this.showLanguageNav === true){
				this.languageMenuTl.seek('start').play();
			} else {
				this.languageMenuTl.seek('50').play();
			}
		},
		handleProductsMenu(){
			this.showProducts = !this.showProducts;
			if(this.showLanguageNav === true){
				this.showLanguageNav = false;
				this.languageMenuTl.seek('50').play();
			}

			if(this.showProducts === true){
				this.trackProductmenu();
				this.productsMenuTl.seek('start').play();
			} else {
				this.productsMenuTl.seek('50').play();
			}
		},
		trackProductmenu(){
			if(this.$store.state.debugTracking){
				console.log('Tracking - Registered product menu click.');
			}
			if(window.utag){
				// COMMENT: Gather all tracking info
				var allSkuNumbers = [],
					allProductNames = [],
					allProductPrices = [],
					allProductCategories = [],
					allProductColors = [],
					allProductUrls = [],
					allProductDescs = [],
					allProductQuantities = [];

				// console.log('locale: ', this.$i18n.locale);
				this.$store.state.products.forEach(product => {
					if(product.hideInMenu === false){

						var sku = product.sku,
							name = this.$t(`${product.sku}_name`),
							desc = this.$t(`${product.sku}_description`),
							price = this.$t(`${product.sku}_price`).replace(/[^\d\.\,]/g, '');

						if(this.$store.state.debugTracking){
							console.log('menu collec - price: ', price);
							console.log('name: ', name);
							console.log('desc: ', desc);
						}

						allSkuNumbers.push(sku);
						allProductNames.push(name);
						allProductPrices.push(price);
						allProductDescs.push(desc);

						allProductQuantities.push('1');
						allProductCategories.push('');
						allProductUrls.push('');
						allProductColors.push('');
					}
				});

				window.utag.link({
					// eCapacity comment: product data of all involved products  see this section for further instructions
					product_sku: allSkuNumbers,
					product_id: allSkuNumbers,
					product_name: allProductNames,
					product_price: allProductPrices,
					product_unit_price: allProductPrices,
					product_quantity: allProductQuantities,

					// Empty but necessary
					product_category: allProductCategories,
					product_color: allProductColors,
					product_url: allProductUrls, // (S/H add place holder and generate url based on same URL that shop now points to)
					product_image_url: allProductUrls,

					estore_event: ['product listing view'],
				});
			}
		},
		onCountryChange(countryCode, event) {
			event.preventDefault();
			this.languageMenuTl.seek('50').play();
			this.showLanguageNav = false;
			const country = this.$store.state.countries.filter(c => c.countryCode === countryCode)[0];
			if(country.campaignUrl) {
				window.location = country.campaignUrl; 

			}
		},
		gotoGlobalPandora(){
			const url = this.$store.state.countries.filter(c => c.countryCode == this.$i18n.locale && c.url)
			window.location = url.length ? url[0].url : Utils.methods.getCountryLink(this.$i18n.locale, this.$store.state.basePandoraUrl);
		}
	},
};
</script>

