	}

	&__headline {
		display: block;
		margin: rem(40) 0 rem(15);
		padding: 0;

		font-size: rem(34);
		font-weight: normal;
		text-align: center;
		color: $color-text;

		@include mq($from: screen-sm){
			font-size: rem(38);
			margin: rem(50) 0 rem(15);
		}
		@include mq($from: screen-md){
			font-size: rem(45);
			margin: rem(60) 0 rem(15);
		}
		@include mq($from: screen-xl){
			font-size: rem(60);
			margin: rem(80) 0 rem(15);
		}
	}

	&__text {
		display: block;
		margin: 0 0 rem(40);
		padding: 0;

		font-size: rem(18);
		text-align: center;
		color: $color-text;

		@include mq($from: screen-md){
			font-size: rem(20);
		}
		// @include mq($from: screen-md){
		// 	//font-size: rem(24);
		// }
		// @include mq($from: screen-xl){
		// 	//font-size: rem(30);
		// }
	}

	&--from-left {
		right: auto;
		left: 0;
		transform: translateX(-102%);

		&:before {
			right: auto;
			left: 0;
		}

		.slide-in-modal__outside-click {
			right: auto;
			left: 0;
		}
	}

	&--auto-width {
		@include mq($from: screen-md) {
			width: auto;
		}
	}

	&.is-active {
		// transform: translateX(0);
		// box-shadow: rem(5) 0 rem(40) 0 rgba(0,0,0,.3);
		box-shadow: rem(5) 0 rem(20) 0 rgba(0,0,0,.3);

		.slide-in-modal__outside-click {
			display: block;
		}
	}

	&__outside-click {
		display: none;

		position: absolute;
		top: 0;
		right: 0;
		z-index: -2;

		width: 100vw;
		height: 100%;
	}
}

.products {
	display: flex;
	flex-flow: row wrap;
	padding: 0;
	margin: 0 0 rem(40);

	li {
		display: block;
		flex: 0 1 50%;
		width: 50%;
		margin-bottom: rem(10);
		list-style-type: none;
		@include mq($from: screen-sm){
			flex: 0 1 25%;
			width: 25%;
		}
		button,
		img {
			display: block;
			width: 100%;
		}
		button {
			border: none;
			outline: none;
			background-color: transparent;
		}
		img {
			transform: scale(1);
			transition: transform .35s ease;
		}
		button:hover img {
			transform: scale(1.1);
		}
	}
}

.close-modal {
	display: block;
	width: rem(16);
	height: rem(16);

	position: relative;
	left: 47%;
	transform: translateX(-30%);

	cursor: pointer;

	opacity: 0;

	@include mq($from: screen-md){
		width: rem(24);
		height: rem(24);
	}

	&:before,
	&:after {
		position: absolute;
		left: rem(8);
		content: ' ';
		height: rem(15);
		width: rem(2);
		background-color: #5e5e5e;

		@include mq($from: screen-md){
			left: rem(13);
			height: rem(23);
			width: rem(3);
		}
	}

	&:before {
		transform: rotate(45deg);
	}

	&:after {
		transform: rotate(-45deg);
	}
}

.site-header__products {
	.close-modal {
		position: absolute;
		top: 50%;
		left: auto;
		right: rem(23);

		transform: translate(0, -50%);

		transition: right .3s, opacity .3s;

		@include mq($from: screen-md){
			right: rem(30);
		}

		@include mq($from: screen-lg){
			right: rem(37);
		}
	}

	.site-header__products__trigger-dots {
		transform: translateX(0%);

		transition: transform .3s ease .15s;

		span {
			transition: opacity .3s ease .15s;
		}
	}
}

.site-header__products.is-active {
	.close-modal {
		right: rem(14);
		opacity: 1;

		transition: right .3s ease .15s, opacity .3s ease .15s;

		@include mq($from: screen-md){
			right: rem(22);
		}

		@include mq($from: screen-lg){
			right: rem(29);
		}
	}

	.site-header__products__trigger-dots {
		transform: translateX(10%);

		transition: transform .3s;

		@include mq($from: screen-md){
			transform: translateX(20%);
		}

		span {
			opacity: 0;

			transition: opacity .3s;
		}
	}
}

.site-header__language-nav {
	.close-modal {
		transition: transform .3s, opacity .3s;
	}

	button .site-header__language-nav__flag {
		transition: transform .3s ease .15s, opacity .3s ease .15s;
	}
}

.site-header__language-nav.is-active {
	.close-modal {
		transform: translateX(-50%);
		opacity: 1;

		transition: transform .3s ease .15s, opacity .3s ease .15s;
	}

	button .site-header__language-nav__flag {
		transform: translate(-70%, -50%);
		opacity: 0;

		transition: transform .3s, opacity .3s;
	}
}

.site-borders {
	width: 0;
	height: 0;

	&__element {
		display: block;
		background-color: $color-white;
		position: fixed;
		z-index: 0;
		transition: $hideSiteHeaderTransition;

		&--left,
		&--right {
			width: rem(15);
			height: 100vh;
			
			@include mq($from: screen-md) {
				width: rem(20);
			}
			@include mq($from: screen-xl) {
				width: rem(30);
			}
		} 
		&--left {
			left: 0;
			top: 0;
		}
		&--right {
			right: 0;
			top: 0;
		}
		&--bottom {
			height: rem(15);
			width: 100vw;
			left: 0;
			bottom: 0;

			@include mq($from: screen-md) {
				height: rem(20);
			}
			@include mq($from: screen-xl) {
				height: rem(30);
			}
		}
	}
}
</style>


