<template>
	<div :id="id" class="flower flower-clover" :class="[position]">
		<lottie :id="id" :options="this.defaultOptions" v-on:animCreated="handleAnimation" v-on:animCompleted="animationCompleted" ref="lottie" />

		<!-- 
		width and height not set on element, so responsive size can be acheived with css
		:height="parseInt(height)" :width="parseInt(width)"
		-->
	</div>
</template>

<script>

import LottieBase from './LottieBase';
import cloverAnimationData from '../../../static/animation-json/clover-mask.json';

export default {
	name: 'CloverAnim',
	components: {
		lottie: LottieBase, // <lottie :options="defaultOptions" :height="400" :width="400" v-on:animCreated="handleAnimation"/>
	},
	props: {
		id: {
			type: String,
			default: null,
		},
		position: {
			type: String,
			default: 'top-right',
		},
		autoplay: Boolean,
		width: String,
		height: String,
	},
	data(){
		return {
			defaultOptions: {
				animationData: cloverAnimationData,
				loop: false,
				autoplay: false,
			},
			animationSpeed: 1,
			direction: 1, // forward animation
			totalFrames: 0, // 186 (null based = 186 - 1 = 185 is the last frame)
			outroStartFrame: 185,
		};
	},
	mounted(){
		if(this.$props.autoplay === true){
			this.anim.play();
		}
		// console.log('clover totalFrames: ', this.totalFrames);
	},

	methods: {
		handleAnimation(anim){ // callback of lottie animCreated
			this.anim = anim;
			this.anim.setDirection(this.direction); // forward animation to start with(might not really be necessary)
			this.totalFrames = this.anim.getDuration(true); // If true, returns duration in frames, if false, in seconds.
		},
		stop(){
			this.anim.stop();
		},
		play(){
			this.$refs.lottie.show();
			this.anim.play();
		},
		playOutro(){
			// console.log('clover outro?');
			this.jumpToFrame(this.outroStartFrame);
			this.setReverse();
			this.anim.play();
		},
		pause(){
			this.anim.pause();
		},
		setReverse(){
			this.anim.setDirection(-1); // Reverse animation
		},
		setForward(){
			this.anim.setDirection(1); // Forward animation
		},
		changeSpeed(speed){
			this.anim.setSpeed(speed ? speed : this.animationSpeed);
		},
		animationCompleted(){
			this.$emit('animationCompleted', this.anim);
		},
		jumpToFrame(frame){
			if(frame < 0){
				frame = 0
			} else if(frame > this.totalFrames){
				frame = this.totalFrames;
			}
			this.anim.goToAndStop(frame, true); // "true" for frame input
		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
	@import '../../assets/settings';

	// .clover-animation-wrapper {
		// width: 40vw;
		// height: 40vw;
		
		// canvas {
		// 	width: 100%;
		// 	height: 100%;
		// }
		// z-index: 3;
	// }
	.flower-clover--top-right,
	.flower-clover--top-left,
	.flower-clover--bottom-left,
	.flower-clover--bottom-right {
		position: absolute;
		z-index: 3;
	}
	.flower-clover--top-left {
		top: 0;
		left: 0;
		// transform: rotate(90deg);
	}
	.flower-clover--top-right {
		top: rem(100);
		right: rem(20);
		transform: rotate(180deg);

		width: rem(120); // as the animation is rotated -90 degrees, width becomes height, and vice versa
		height: rem(120);

		@include mq($from: screen-xs){
			width: rem(200);
			height: rem(200);
		}
		@include mq($from: screen-sm){
			width: rem(250);
			height: rem(250);
		}
		@include mq($from: screen-md){
			width: rem(439);
			height: rem(409);
		}
		@include mq($from: screen-lg){
			width: rem(606); // as the animation is rotated -90 degrees, width becomes height, and vice versa
			height: rem(639);
		}
		@include mq($from: screen-xl){
			width: rem(606); // as the animation is rotated -90 degrees, width becomes height, and vice versa
			height: rem(639);
		}
	}
	.flower-clover--bottom-left {
		bottom: 0;
		left: 0;

		transform: rotate(-90deg);
	}
	.flower-clover--bottom-right {
		bottom: 0;
		right: 0;

		transform: scaleX(-1) rotate(-90deg);
	}
</style>



