export default {
	closeOpenModal(context){
		context.commit('closeModal');
	},
	openClosedModal(context, payload){
		if(payload.contentType == undefined){
			context.commit('openModal', payload);
		} else {
			if(payload.contentType != 'video'){
				context.commit('openModal', payload);
			}
		}
	},
	registerShopClick(skuNumber){
		if(state.debugTracking){
			console.log('Tracking - Registered shop now click. Sku: ', skuNumber || null);
		}
		if(window.utag){
			window.utag.link({
				// eCapacity comment: product data of all involved products  see this section for further instructions
				// product_sku: [skuNumber],
				estore_event: ['Pandora Garden shop the collection'],
			});
		}
	},
	registerProductClick({ commit, state }, payload){
		var product = payload.product;
		if(state.debugTracking){
			console.log('Tracking - Registered product now click. Product: ', product);
		}
		if(window.utag){
			var sku = product.sku,
				name = product.name,
				desc = product.description,
				price = product.price.replace(/[^\d\.\,]/g, '');

			if(state.debugTracking){
				console.log('sku - shopnow: ', sku);
				console.log('name - shopnow: ', name);
				console.log('price - shopnow: ', price);
				console.log('desc - shopnow: ', desc);
			}
			window.utag.link({
				// eCapacity comment: product data of all involved products  see this section for further instructions
				product_sku: [sku],
				product_id: [sku],
				product_name: [name],
				product_price: [price],
				product_unit_price: [price],
				product_quantity: ['1'],
				product_description: [desc],

				// Empty but necessary
				product_category: [''],
				product_color: [''],
				product_url: [''], // (S/H add place holder and generate url based on same URL that shop now points to)
				product_image_url: [''],

				estore_event: ['Pandora Garden shop now'],
			});
		}

		window.open(`${state.pandoraUrl + product.sku}.html`, '_blank');
	},
};


// * product_sku (already set)
// * product_id (same as product_sku)
// * product_name (S/H to add placeholder for data, PANDORA will provide data)
// * product_price (S/H to add placeholder for data, PANDORA will provide data)
// * product_unit_price (same as product_price)
// * product_quantity (is always 1 on the Pandora Garden page)
// * product_category (S/H to add placeholder for data, PANDORA will provide data) (id - hashed ngle)
// * product_color (Not really important as it can be derived from the product SKU)
