<!--
	/*
	Trigger description:

		Trigger points are set based on a fixed position of the scene, either top or bottom.
	*/
-->

<template>
	<div class="application-frame">
		<div class="image-preload" :style="`background-image: url('${imageRoot}butterfly-video-poster.jpg'),url('${imageRoot}ladybird-video-poster.jpg'),url('${imageRoot}clover-video-poster.jpg'),url('${imageRoot}blossom-video-poster.jpg'),url('${imageRoot}branded-video-poster.jpg');`"></div>
		<div class="landscape-disable" ref="landscapeDisable" v-if="showLandscapeDisable">
			<div class="landscape-disable__inner">
				<div class="landscape-disable__title">Please rotate your device</div>
				<svg width="86" height="78" viewBox="0 0 86 78" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
					<defs>
						<path d="M5 0C2.247 0 0 2.247 0 5v68c0 2.753 2.247 5 5 5h76c2.753 0 5-2.247 5-5V41c0-2.753-2.247-5-5-5H42V5c0-2.753-2.247-5-5-5H5zm66.313 28.405a.657.657 0 0 1-1.307-.123c0-8.66-6.692-15.75-15.201-16.324l2.043 3.575a.659.659 0 1 1-1.144.654l-2.615-4.577a.654.654 0 0 1 .245-.899l4.576-2.615a.654.654 0 0 1 .981.695.654.654 0 0 1-.327.45l-2.656 1.511c8.688 1.101 15.405 8.547 15.405 17.53v.123zM5 2h32c1.68 0 3 1.32 3 3v1H2V5c0-1.68 1.32-3 3-3zM2 8h38v56H2V8zm40 30h36v38H40.875c.666-.851 1.125-1.84 1.125-3V38zm38 0h1c1.68 0 3 1.32 3 3v32c0 1.68-1.32 3-3 3h-1V38zM2 66h38v7c0 1.21-.71 2.238-1.719 2.719a1 1 0 0 0-.343.281H5c-1.68 0-3-1.32-3-3v-7zm15.75 4a1.006 1.006 0 0 0 .219 2h6.062a1 1 0 1 0 0-2H17.97a1 1 0 0 0-.125 0 1.002 1.002 0 0 0-.094 0z" id="landscape-a"/>
					</defs>
					<g fill="none" fill-rule="evenodd">
						<mask id="landscape-b" fill="#fff">
							<use xlink:href="#landscape-a"/>
						</mask>
						<use fill="#686C6F" fill-rule="nonzero" xlink:href="#landscape-a"/>
						<path d="M42 36h44v42.002H37.007c3.567 0 4.993-3.732 4.993-4.556V36z" fill="#fff" opacity=".429" mask="url(#landscape-a)"/>
					</g>
				</svg>
			</div>
		</div>
		<Modal v-if="modal.display"></Modal>
		<main id="garden" @click="modalPosition">
			<div class="video-wrapper" ref="videoWrapper">
				<video muted="true" loop="true" playsinline="true" preload="auto" autoplay :src="introVideo" ref="mainVideo" id="main-video" class="main-video" :poster="`${imageRoot}butterfly-video-poster.jpg`"></video>

				<!-- <video muted="true" loop="true" playsinline="true" preload="auto" autoplay :src="`${staticRoot}video/butterfly-short.mp4`" ref="butterfly-video" id="butterfly-video" class="video"></video>

				<video muted="true" loop="true" playsinline="true" preload="auto" :src="`${staticRoot}video/ladybird-short.mp4`" ref="ladybird-video" id="ladybird-video" class="video"></video>

				<video muted="true" loop="true" playsinline="true" preload="auto" :src="`${staticRoot}video/clover-short.mp4`" ref="clover-video" id="clover-video" class="video"></video>

				<video muted="true" loop="true" playsinline="true" preload="auto" :src="`${staticRoot}video/blossom-short.mp4`" ref="blossom-video" id="blossom-video" class="video"></video>

				<video muted="true" loop="true" playsinline="true" preload="auto" :src="`${staticRoot}video/butterfly-OLD-TEST.mp4`" ref="branded-video" id="branded-video" class="video"></video> -->

				<div class="video-filter"></div>
				<div class="spinner video-spinner loading"></div>
			</div>
			<!-- <div class="bg bg--main"></div> -->

			<Intro ref="intro" v-on:allFlowersLoaded="allFlowersLoaded" />

			<template v-for="(scene, index) in scenes">
				<SceneHero
					v-if="scene.contentType === 'hero'"
					:options="scene"
					:className="scene.uniqueClass"
					:ref="`${ scene.contentId }`"
					:firstHero="index===0"
					/>
					<!-- BACKUP v-on:firstVideoReady="firstVideoReady" -->

				<SceneContentButterfly
					v-if="scene.contentId === 'butterfly-content'"
					:options="scene"
					:className="scene.uniqueClass"
					:ref="`${ scene.contentId }`"
					/>

				<SceneContentLadybird
					v-if="scene.contentId === 'ladybird-content'"
					:options="scene"
					:className="scene.uniqueClass"
					:ref="`${ scene.contentId }`"
					/>

				<SceneContentClover
					v-if="scene.contentId === 'clover-content'"
					:options="scene"
					:className="scene.uniqueClass"
					:ref="`${ scene.contentId }`"
					/>

				<SceneContentBlossom
					v-if="scene.contentId === 'blossom-content'"
					:options="scene"
					:className="scene.uniqueClass"
					:ref="`${ scene.contentId }`"
					/>

				<SceneFooter
					v-if="scene.contentId === 'footer'"
					:options="scene"
					:ref="`${ scene.contentId }`"
					/>
			</template>
		</main>
		<transition name="slideout" >
			<article v-if="!cookieAccepted" class="cookie-banner">
				<div class="cookie-banner__inner">
					<div class="cookie-banner__content">
						{{ $t('cookie_content') }}
						<br>
						<a :href="$t('cookie_policy_link_url')">{{ $t('cookie_policy_link_text') }}</a>
					</div>
					<div class="cookie-banner__button-wrapper">
						<button class="cookie-accept" @click="hideCookieBanner">{{ $t('cookie_accept_button_text') }}</button>
					</div>
				</div>
			</article>
		</transition>
		<SiteHeader />
	</div>
</template>

<script>
import { isCookieEnabled, getCookie, setCookie, removeCookie } from 'tiny-cookie'
import {mapState } from 'vuex';
import * as constants from './constants.js';

// import GSDevTools from 'GSDevTools';
// import { TimelineMax } from 'gsap';

import SiteHeader from './components/SiteHeader';
// import InstagramFeed from './components/InstagramFeed';

import Modal from './components/modal';
import Intro from './components/scenes/Intro';
import SceneHero from './components/scenes/SceneHero';
import SceneContent from './components/scenes/SceneContent';
import SceneContentButterfly from './components/scenes/SceneContentButterfly';
import SceneContentLadybird from './components/scenes/SceneContentLadybird';
import SceneContentClover from './components/scenes/SceneContentClover';
import SceneContentBlossom from './components/scenes/SceneContentBlossom';
import SceneFooter from './components/scenes/SceneFooter';

import * as Constants from './constants';

export default {
	name: 'Garden',
	components: {
		Modal,
		SiteHeader,
		Intro,
		SceneHero,
		SceneContent,
		// InstagramFeed,
		SceneContentButterfly,
		SceneContentLadybird,
		SceneContentClover,
		SceneContentBlossom,
		SceneFooter,
	},
	data(){
		return {
			// debug/dev vars
			staticRoot: this.$store.state.staticRoot,
			imageRoot: this.$store.state.imageRoot,
			showTriggerPositions: false, // scroll/trigger position debug
			showIntro: this.$store.state.showIntro,

			masterHeight: 0, // getting passed to the state after first render/mounted
			scenes: this.$store.state.scenes,
			modal: this.$store.state.modal,

			// instagramFeed: this.$store.state.instagram.feed,
			// instagramToken: this.$store.state.instagram.token,
			hideSiteHeader: this.$store.state.hideSiteHeader,
			cookieAccepted: false,
			
			showLandscapeDisable: false,
			introVideo: !this.$store.state.IEorEdge ? this.$store.state.scenes.filter(sc => sc.contentId == 'butterflyHero')[0].heroSrc : this.$store.state.scenes.filter(sc => sc.contentId == 'brandedVideo')[0].heroSrc.desktopSmall,
		
		};
	},
	computed: {
		// progress: state => state.currentProgress, 
		// disableScroll: state => state.disableScroll,
		progress(){
			return this.$store.state.currentProgress;
		},
		videoReady(){
			// console.log('video ready computed');
			return this.$store.state.firstVideoReady;
		},
		waitTimeDone(){
			// console.log('wait time computed');
			return this.$store.state.introWaitDone;
		},
		allDone(){
			return this.$store.state.firstVideoReady && this.$store.state.introWaitDone;
		},
		disableScroll: {
			get: function() {
				return this.$store.state.disableScroll;
			},
			set: function(bool) {
				this.$store.commit({type: 'disableScroll', bool})
			},
		},
	},
	watch: {
		videoReady(){
			// console.log('video ready watched');
			if(this.$store.state.introWaitDone){
				this.runIntro();
			}
		},
		waitTimeDone(){
			if(this.$store.state.firstVideoReady){
				this.runIntro();
			}
			// console.log('wait time watched');
		},
		allDone(oldDone, newDone){
			if(newDone){
				this.runIntro();
			}
			// return this.firstVideoReady && this.
		},
		disableScroll: function() {
			const $body = document.body;
			if(this.disableScroll) {
				window.scrollTo(0,0);
				$body.style.maxHeight = '0px';
				$body.style.height = '0px';
				$body.style.overflow = 'hidden';
				return;
			}
			$body.style.height = this.$store.state.masterHeight + 'px';
			$body.style.maxHeight = 'none';
			$body.style.overflow = 'auto';
		},
	},
	created(){
		// COMMENT: Doing tracking setup
		if(this.$store.state.debugTracking){
			console.log('Register app tracking data');
			// console.log('state: ', this.$store.state);
		}
		
		// console.log('index of: ', this.$store.state.cookieLocales.indexOf(this.$i18n.locale));

		// COMMENT: Not in the EU, so no cookie policy banner
		this.cookieAccepted = getCookie(constants.COOKIE_CONCENT) || this.$store.state.cookieLocales.indexOf(this.$i18n.locale) === -1 ? true : false;
	},
	mounted(){
		this.handleLandscapeDisable(); // COMMENT: Test if landscape mobile, and show overlay
		var manipulatedWindowHeight = window.innerHeight;
		var windowWidth = window.innerWidth;
		// console.log('IE: ', this.$store.state.IEorEdge)

		// if(window.matchMedia('(max-width: 1024px)').matches){ // COMMENT: When mobile, add some extra height to prevent size-jump when address-bar disappears
		// 	// manipulatedWindowHeight += 63; // COMMENT: ipad address-bar and tabs take up 63px
		// 	manipulatedWindowHeight += 63; // COMMENT: iPhone X address-bar and tabs take up ~83px
		// }

		if(window.matchMedia('(max-width: 1024px)').matches){ // COMMENT: When mobile, add some extra height to prevent size-jump when address-bar disappears
			// manipulatedWindowHeight += 63; // COMMENT: ipad address-bar and tabs take up 63px
			manipulatedWindowHeight += 115; // COMMENT: iPhone X address-bar and tabs take up ~115px
		}
		this.$refs.videoWrapper.style.height = manipulatedWindowHeight + 'px';
		this.$refs.mainVideo.style.height = manipulatedWindowHeight + 'px';

		this.$store.commit({
			type: 'windowHeight',
			windowHeight: manipulatedWindowHeight,
		});

		this.showTriggerPositions = ( this.$store.state.appLogLevel > 0 ? true : false );

		this.$nextTick(this.setupScenes);
		this.$nextTick(this.setupScroll);

		if(this.showIntro === false){ // for dev, if no intro, go directly to butterflyhero intro
			this.$nextTick(this.introComplete);
		}
		// update links in the store with new locale info
		this.$store.commit({ type: 'updateBaseLinks', locale: this.$i18n.locale });

		// COMMENT: Add GSAP debug for ability to "scroll" in TimelineMax progress
		// GSDevTools.create(); // remember gsdevtools import above

		// COMMENT: Force update(re-render) this component, not affecting child-components
		if(this.$store.state.appLogLevel === 0){
			window.addEventListener('resize', function() {
				// this.$forceUpdate();
				// console.log('newWidth: ',window.innerWidth);
				// console.log('oldWidth: ',windowWidth);
				// console.log('this.handleLandscapeDisable(): ', this.handleLandscapeDisable());
				if(window.innerWidth !== windowWidth){
					if(this.handleLandscapeDisable()){
						// do nothing and show the rotate icon and description
					} else {
						// console.log('reloading?');
						// window.reload();
						window.location.reload(true); // forceReload = true > forces scrollTo(0,0)!! (most of the time anyway...)
					}
				} else {
					// do something/nothing when returning to old position
				}
				windowWidth = window.innerWidth; // COMMENT: Used when reload should accour
			}.bind(this));
		}

		// this.$nextTick(this.setupIntroWaitTime);
	},

	methods: {
		hideCookieBanner(){
			setCookie(constants.COOKIE_CONCENT, 'true', { expires: '6M' });
			this.cookieAccepted = true;
		},
		allFlowersLoaded(){
			// console.log('app - all flowers loaded callback');
			this.setupIntroWaitTime();
		},
		runIntro(){
			// console.log('-------------------------------------------------------- videoReady');
			if(this.$store.state.firstVideoReady){
				// this.$refs.intro.$refs.headline.$refs.introSpinner.classList.add('hidden');
				// this.$refs.intro.$refs.headline.$refs.introSpinner.remove(); // COMMENT: IE11 does not like this .remove, and it's not really neccessary

				this.$refs.intro.$refs.headline.$refs.introSpinner.classList.add('hidden');

				this.$refs.intro.runOutro();

				this.$store.commit({
					type: 'showMenu',
					hide: false
				});

				this.$refs.butterflyHero[0].playIntro();
			}
		},
		introComplete(){
			// COMMENT: When intro's intro is done, play butterflyhero's intro
			// console.log('this.$refs.butterflyHero[0]: ', this.$refs.butterflyHero[0]);
			
		},
		handleLandscapeDisable(){
			this.showLandscapeDisable = window.matchMedia('(max-width: 1024px) and (orientation: landscape)').matches ? true : false;
			return this.showLandscapeDisable;
		},
		setupIntroWaitTime(){
			setTimeout(()=>{
				// console.log('intro wait is now done...');
				this.$store.state.introWaitDone = true;
			}, this.$store.state.introWaitTime);
		},
		setupScenes(){
			if(this.$store.state.introHijackScroll === true){
				this.disableScroll = true;
				// window.scrollTo(0,0); // force page to the top
				// document.body.style.maxHeight = '100vh';
				// document.body.style.maxHeight = '0px';
				// document.body.style.height = '0px';
				// document.body.style.overflow = 'hidden';
			}
			let masterHeight = 0;

			var sceneDurationModifier = 1.5; // for smaller screen sizes we fake that the scene is longer for scrolling
			if (window.matchMedia('(max-width: 768px)').matches) {
				sceneDurationModifier = 1.5;
			} else if (window.matchMedia('(max-width: 1024px)').matches) {
				sceneDurationModifier = 1.3;
			} else if (window.matchMedia('(max-width: 1366px)').matches) {
				sceneDurationModifier = 1;
			} else {
				sceneDurationModifier = 1;
			}
			this.scenes.forEach(function(scene){
				scene.sceneTopOffset = masterHeight;

				scene.sceneHeight = (this.$store.state.windowHeight * (scene.sceneHeight / 100)) * sceneDurationModifier;

				// COMMENT: Setup trigger positions based on scene position in master timeline flow
				const defaultTriggerOffset = scene.sceneTopOffset;

				for (const trigger in scene.triggers) {
					// loop scene triggers
					if (scene.triggers.hasOwnProperty(trigger)) {
						const triggerOffsetValue = scene.triggers[trigger].value * sceneDurationModifier;
						const offsetFrom = scene.triggers[trigger].offsetFrom;

						if (typeof triggerOffsetValue == 'number') {
							// COMMENT: If boolean aka. false, trigger-property not in use
							if (offsetFrom === 'top') {

								if (triggerOffsetValue === 0) {
									scene.triggers[trigger].value = Math.floor(
										defaultTriggerOffset
									);
								} else {
									const vhPixel =
										defaultTriggerOffset +
										this.$store.state.windowHeight * (triggerOffsetValue / 100);
										// var convertToPositive = triggerOffsetValue * 1;
									scene.triggers[trigger].value = Math.floor(vhPixel);

								}
							} else if (offsetFrom === 'bottom') {
								const bottomValue = defaultTriggerOffset + scene.sceneHeight;
								// console.log('bottomValue: ', bottomValue);
								if (triggerOffsetValue === 0) {
								scene.triggers[trigger].value = bottomValue;
								} else {
									var vhPixel =
										bottomValue +
										this.$store.state.windowHeight * (triggerOffsetValue / 100);
									scene.triggers[trigger].value = Math.floor(vhPixel);
								}
							}

							// console.log('reverse trigger? ', trigger.toLowerCase().indexOf('reverse'));
							if (trigger.toLowerCase().indexOf('reverse') < 0) {
								// if trigger is not a reverse trigger, put the value to test for testing min and max height scene
								scene.triggerValues.push(scene.triggers[trigger].value);
							}

							// show scene triggers in the DOM
							if(this.showTriggerPositions){
								if (
									(trigger == 'prepare') ||
									(trigger == 'intro') ||
									(trigger == 'outro') ||
									(trigger == 'prepareReverse')
								){
									var triggerElem = document.createElement('div');
									triggerElem.innerHTML = scene.name + ': ' + trigger;
									triggerElem.style.top = scene.triggers[trigger].value + 'px';
									triggerElem.classList.add('painted-trigger');
									triggerElem.classList.add('trigger-' + trigger);
									document.body.append(triggerElem);
								}
							}
						}
					}
				}
				const introOffset = (scene.triggers.intro.value ? scene.triggers.intro.value : 0) - masterHeight;
				masterHeight += scene.sceneHeight + introOffset;
				// console.log('masterHeight: ', masterHeight);

			}.bind(this));

			// COMMENT: Can't seem to get this commit working
			// this.$store.commit({
			// type: 'masterHeight',
			// masterHeight
			// });

			masterHeight = (masterHeight + (this.$store.state.windowHeight * 1)); // FOOTER VISIBILITY HOTFIX
			// console.log('accumulated masterHeight: ', masterHeight);
			this.$store.state.masterHeight = masterHeight;
			if(this.$store.state.introHijackScroll === false){ // in DEV set the body height to masterHeight
				this.disableScroll = false;
				// document.body.style.height = this.$store.state.masterHeight + 'px';
				// document.body.style.maxHeight = 'none';
				// document.body.style.overflow = 'auto';
			}

			this.$store.commit('appLoaded', true, this.$i18n.locale);
		},
		modalPosition(event) {
			this.$store.state.modal.position = {
				x: event.clientX,
				y: event.clientY,
			};
		},
		setupScroll() {
			// setting progression
			let lastKnownScrollPosition = 0;
			let oldScrollPosition = 0;
			let scrollTicking = false;

			if('ontouchstart' in window) {
				this.intervalId = window.setInterval(()=>{
					if(lastKnownScrollPosition === window.scrollY) return;
					lastKnownScrollPosition = window.scrollY;
					window.requestAnimationFrame(() => {
						oldScrollPosition = this.scrollHandler(
							oldScrollPosition,
							lastKnownScrollPosition
						);
					});
				}, this.$store.tickInterval);
			} else {
				window.addEventListener('scroll', () => {
					lastKnownScrollPosition = window.scrollY;
	
					if (!scrollTicking) {
						window.requestAnimationFrame(() => {
							oldScrollPosition = this.scrollHandler(
								oldScrollPosition,
								lastKnownScrollPosition
							);
	
							scrollTicking = false;
						});
	
						scrollTicking = true;
					}
				}, false);
			}
		},

		scrollHandler(oldScrollPosition, scrollPos) {
			let progressPercent = (scrollPos / (this.$store.state.masterHeight - this.$store.state.windowHeight)) * 100;
			progressPercent = progressPercent.toFixed(4);

		  	const pixelProgress = scrollPos;

		  	const direction = scrollPos > oldScrollPosition ? constants.FORWARD : constants.BACKWARD;
		  	this.$store.commit('currentProgress', {
				percent: progressPercent,
				pixels: Math.floor(pixelProgress),
				direction,
			});
			this.setActiveSceneState(scrollPos, direction);
			return scrollPos;
		},

		setActiveSceneState(scrollPos, direction) {
			// console.log("scrollPos: ", scrollPos);
			this.scenes.forEach((scene, sceneIterator) => {
				// intro state active
				if (
					scene.triggers.intro.value &&
					scrollPos >= scene.triggers.intro.value &&
					scrollPos <= (scene.sceneHeight + scene.triggers.intro.value)) {
					// console.log('intro: ', scene.name);

					this.$store.commit({
						type: 'sceneState',
						sceneIterator,
						state: constants.ONGOING,
					});
				} else {
					this.$store.commit({
						type: 'sceneState',
						sceneIterator,
						state: constants.OUT_OF_VIEW,
					});
				}

				// Prepare trigger
				if (
					scene.triggers.prepare.value !== false &&
					scene.triggers.prepareReverse.value !== false &&
					scrollPos >= scene.triggers.prepare.value &&
					scrollPos <= scene.triggers.prepareReverse.value &&
					!scene.isPrepared
					){
					this.$store.commit({
						type: 'prepareState',
						sceneIterator,
						state: constants.PREPARE
					});
				}

				// reset trigger
				if (
					scene.isPrepared &&
					((scene.triggers.prepare.value != false &&
					scrollPos <= scene.triggers.prepare.value) ||
					(scene.triggers.prepareReverse.value != false &&
					scrollPos >= scene.triggers.prepareReverse.value))
				){
					this.$store.commit({
						type: 'prepareReset',
						sceneIterator,
					});
				}
			});
		},
	},
};
</script>

<style lang="scss">
@import './assets/global';

