	z-index: 999;
	
	@include mq($from: screen-xs){
		padding: rem(15) rem(30);
	}

	&.slideout-leave-active {
		transform: translate3d(0, 100%, 0);
	}
	&__inner {
		display: flex;
		flex-wrap: no-wrap;
		justify-content: space-between;
	}
	&__content {
		color: $color-white;
		font-size: rem(15);
