		max-width: rem(550);

		a {
			color: $color-white;
			text-decoration: underline;
			transition: color .25s ease;

			&:hover {
				color: #ddd;
			}
		}
	}
	&__button-wrapper {
		min-width: rem(120);
	}
	.cookie-accept {
		background-color: #fff;
		padding: rem(10) rem(14);
		color: #333;
		border-radius: rem(2);
		cursor: pointer;
		font-family: $font-stack-bold;
		font-size: rem(15);


		transition: background-color .25s ease;

		@include mq($from: screen-xs){
			padding: rem(12) rem(25);
		}

		&:hover {
			background-color: #ddd;
		}
	}
}

.video-wrapper {
	width: 100%;
	height: 100vh;
	// margin-bottom: 50vh;
	z-index: 1;
	overflow: hidden;
	pointer-events: none;
	user-select: none;

	video {
		position: fixed;
		top: 0;
		left: 0;

		width: 100%;
		height: calc(100% + 63px); // 63 is a wild guess on how tall the retractable address-bar on iPhone/5/6/7/8 and iPad is
		object-fit: cover;
		object-position: 50% 50%;

		z-index: 0;

		@include mq($from: screen-sm){
			height: calc(100% + 83px); // iPhone X has an extra bar at the bottom
		}

		// background-color: $color-carousel-pink;

		// &.video-active { // used when multiple video-tags
		// 	z-index: 1;
		// }
	}
	#main-video {
		z-index: 2;
	}
	// .video { // all other videos than master(in which the url is changed) // used when multiple video-tags
	// 	opacity: 0;
	// 	visibility: hidden;
	// }
	.video-filter {
		position: fixed;
		top: 0;
		left: 0;

		width: 100%;
		height: calc(100% + 63px);

		background-color: rgba(50, 50, 50, 0.3);
		z-index: 3;
	}
	.video-spinner {
		display: none;
		position: fixed;
		right: 8%;
		bottom: 4%;
		z-index: 4;
		font-size: rem(24);

		@include mq($from: screen-sm){
			font-size: rem(30);
			right: 6%;
		}
		@include mq($from: screen-md){
			font-size: rem(36);
		}
		@include mq($from: screen-xl){
			// font-size: rem(44);
			border-width: rem(2);
			right: 4%;
		}

		&.loading {
			display: block;
		}
	}
}

.landscape-disable {
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background-color: $color-white;

	z-index: 99999;

	&__inner {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		color: $color-text;
		text-align: center;
	}
	&__title {
		font-size: rem(20);
		margin-bottom: rem(15);
	}
}

.painted-trigger { // DEV
	position: absolute;
	top: 0; // gets overwritten by individual triggers
	right: 0;
	border-bottom: rem(1) solid green;
	z-index: 6;

	&.trigger-prepare {
		right: rem(100);
		color: red;
	}
}
</style>



