<template>
	<section class="scene-content" :class="[className, inView ? 'active-pointer' : '']">
		<div class="bg" ref="bg"></div>

		<h2 class="scene-headline" ref="headline">{{ $t('butterfly_content_headline') }}</h2>

		<ButterflyAnimation
			ref="butterfly1"
			id="butterfly1"
			position="butterfly1"
			width=500
			height=500
			:sequenceDelay="3000"
			v-if="mq.md"
		/>
		<!-- ROEN: Just an example usage of animationCompleted callback <ButterflyAnimation
			ref="butterfly1"
			v-on:animationCompleted="butterflyAnimationCompleted"
		/> -->
		<ButterflyAnimation
			ref="butterfly2"
			id="butterfly2"
			position="butterfly2"
			width=250
			height=250
			:sequenceDelay="2800"
			v-if="mq.md"
		/>
		<ButterflyAnimation
			ref="butterfly3"
			id="butterfly3"
			position="butterfly3"
			width=300
			height=300
			:sequenceDelay="1800"
			v-if="mq.xl"
		/>
		<FlowerTwoLeaves
			ref="FlowerTwoLeaves"
			id="flower-twoleaves"
			position="flower-twoleaves--butterfly"
			width=400
			height=500
			v-if="mq.md"
			/>
		<div class="content" ref="content">
			<div class="butterfly-card" :class="card.contentType == 'video' ? 'video' : ''" v-for="(card, index) in cards" @click="openClosedModal({sku: card.sku, isCard: true, target: $event.target, contentType: card.contentType, sceneType: 'butterfly'})" :key="index">
				<div class="butterfly-card__image-wrapper custom-cursor" v-if="card.contentType == 'image'">
					<img :src="card.images.cardThumb" alt="Butterfly image 1">
				</div>
				<div class="text custom-cursor" v-if="card.cardText">
					{{ $t( card.cardText ) }}
				</div>
				<div class="butterfly-card__video-wrapper" v-if="card.contentType == 'video'" :style="'padding-top: ' + card.video.thumbFormat + ';'">
					<video :src="card.video.thumb" muted="muted" loop="loop" playsinline="true" :ref="'video' + (index + 1)"></video>
				</div>
			</div>
		</div>
	</section>
</template>

<script>
	import { mapState, mapActions } from 'vuex';
	import ProgressMixin from '../mixins/ProgressMixin';

	import { ONGOING } from '../../constants';

	import SceneHeadline from '../SceneHeadline';
	import ButterflyAnimation from '../animations/Butterfly';
	import FlowerTwoLeaves from '../animations/FlowerTwoLeaves';

	export default {
		name: 'SceneContentButterfly',
		mixins: [ProgressMixin],
		components: {
			SceneHeadline,
			ButterflyAnimation,
			FlowerTwoLeaves,
		},
		props: {
			options: Object,
			className: String,
			content: Object,
		},
		mounted() {
			// console.log('mounted - this.options.name: ', this.options.name);
			
			// console.log('contentWidth: ', this.contentWidth);
			this.$nextTick(this.setupScene);
		},
		data() {
			return {
				mq: this.$store.state.mq,
				// contentWidth,
				// windowWidth,
				// inView: this.$store.scenes.filter(store => store.name === this.props.options.name)[0].state,
			};
		},
		computed: mapState({
			cards: state => state.products.filter(product => product.type === 'butterfly'),
		}),
		methods: {
			...mapActions([
				'openClosedModal',
			]),
			butterflyAnimationCompleted() {
				// console.log('butterfly animation complete');
			},
			// runPrepare(prepareProgress){
			// 	if(this.scenePrepareRan === false && prepareProgress == true){
			// 		// console.log('now preparing - scene: ', this.options.name);
			// 		this.scenePrepared = true;
			// 		this.scenePrepareRan = true;
			// 	}
			// },
			// runPrepareReverse(prepareProgress){
			// 	if(this.scenePrepareReverseRan === false && prepareProgress == true){
			// 		// console.log('now preparing - scene: ', this.options.name);
			// 		this.sceneReversePrepared = true;
			// 		this.scenePrepareReverseRan = true;
			// 	}
			// },

			setupScene() {
				const bg = this.$refs.bg;
				const headline = this.$refs.headline;
				const butterfly1 = this.$refs.butterfly1;
				const content = this.$refs.content;

				const wh = this.$store.state.windowHeight;
				const contentHeight = content.clientHeight;

				this.contentWidth = this.$refs.content.clientWidth;
				this.windowWidth = window.innerWidth;

				// this.introTl
				// 	.set(content, {autoAlpha: 0});

				// console.log('content scene: ', this.options.name);

				var tlProgress = 0;
				var scrollDirection = 'down';
				this.introTl = new TimelineMax({paused: true, onUpdateParams: ['{self}'], onUpdate: function(introTl) {
					var currentTlProgress = introTl.progress();
					if(currentTlProgress > tlProgress){
						scrollDirection = 'down';
					} else {
						scrollDirection = 'up';
					}
					tlProgress = introTl.progress();
				}});

				this.introTl
					.set(bg, { autoAlpha: 0 });

				this.introTl
					.add('intro-start')
					.to(bg, 1, {
						autoAlpha: 1,
						ease: Linear.easeNone,
					})
					.add('content-start')
					.addLabel('play-card-videos', 'content-start')
					.addLabel('butterfly1-intro', 'content-start+=0.3') // timing cannot be 0('intro-start'), as it doesn't provoke the direction
					.addLabel('butterfly2-intro', 'content-start-=0.1')
					.addLabel('butterfly3-intro', 'content-start-=0.3')
					.addLabel('twoleaves-intro', 'content-start-=0.3')
					;

				if(window.matchMedia('(max-width: 1023px)').matches){
					// console.log('detecting mobile');
					// console.log('wh: ', wh);
					// console.log('contentHeight: ', contentHeight);
					// console.log('(contentHeight + wh): ', (contentHeight + wh));
					// console.log('(contentHeight + (wh * 2)): ', (contentHeight + (wh * 2)));
					this.introTl
						.fromTo(content, 7,
							{
								// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
								// y: '50%',
								x: '-50%',
								y: `${wh}px`,
								autoAlpha: 1,
								// ease: Linear.easeNone,
							},
							{
								// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
								// y: '50%',

								// x: '-' + this.contentWidth - 1800 + 'px',
								// x: `-${this.contentWidth}px`,
								x: '-50%',
								y: `-${(contentHeight * 1.2)}px`,
								autoAlpha: 1,
								ease: Linear.easeNone,
							},
							'intro-start',
						)
						;
				} else {
					// console.log('detecting desktop');
					this.introTl
						.fromTo(content, 5,
							{
								// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
								// y: '50%',
								x: `${this.windowWidth}px`,
								// y: `${wh}px`,
								autoAlpha: 1,
								// ease: Linear.easeNone,
							},
							{
								// y: '+=' + this.$store.state.windowHeight / 3 + 'px',
								// y: '50%',

								// x: '-' + this.contentWidth - 1800 + 'px',
								x: `-${this.contentWidth + (this.windowWidth * .5)}px`,
								// y: `-${contentHeight}px`,
								autoAlpha: 1,
								ease: Linear.easeNone,
							},
							'intro-start',
						)
						;
				}

				this.introTl
					.fromTo(headline, .5,
						{
							autoAlpha: 0,
							ease: Linear.easeNone,
						},
						{
							autoAlpha: 1,
							ease: Linear.easeNone,
						},
						'intro-start+=0.6',
					)
					.add('outro')
					.addLabel('butterfly1-outro', 'outro-=0.8') // timing cannot be 0, as it doesn't provoke the direction
					.addLabel('butterfly2-outro', 'outro-=1.1')
					.addLabel('butterfly3-outro', 'outro-=1.4')
					.addLabel('twoleaves-outro', 'outro-=1.8')
					.addLabel('stop-card-videos', 'outro')
					.to(bg, 1, {
						autoAlpha: 0,
						delay: -0.7,
						ease: Linear.easeNone,
					})
					;

				if(window.matchMedia('(max-width: 1023px)').matches){
					// COMMENT: Do mobile stuff
					this.introTl
						.to(headline, 0.5,
							{
								autoAlpha: 0,
								// delay: -1.2,
								ease: Linear.easeNone,
							},
							'intro-start+=1.2'
						)
						;
				} else {
					this.introTl
						.to(headline, 0.5,
							{
								autoAlpha: 0,
								delay: -1.2,
								ease: Linear.easeNone,
							},
						)
						;
				}

				this.introTl
					.add('end')
					.progress(1, true) // cache animation, but don't do callbacks (the true parameter is suppressing events)
					.progress(0, true)
					// .pause()
					;

				this.introTl.addCallback(() => { // COMMENT: Start and stop card videoes when in/out of view
					if(scrollDirection == 'down'){
						this.$refs.video4[0].play();
						// this.$refs.video6[0].play();
					} else {
						this.$refs.video4[0].pause();
						// this.$refs.video6[0].pause();
					}
				}, 'play-card-videos');
				this.introTl.addCallback(() => {
					if(scrollDirection == 'down'){
						this.$refs.video4[0].pause();
						// this.$refs.video6[0].pause();
					} else {
						this.$refs.video4[0].play();
						// this.$refs.video6[0].play();
					}
				}, 'stop-card-videos');

				if(this.mq.md){ // COMMENT: media query check, same as on the animation-components in the template
					var butterflyAnimations = [
						{
							el: this.$refs.butterfly1,
							introLabel: 'butterfly1-intro',
							outroLabel: 'butterfly1-outro',
						},
						{
							el: this.$refs.butterfly2,
							introLabel: 'butterfly2-intro',
							outroLabel: 'butterfly2-outro',
						},
						{
							el: this.$refs.butterfly3, // is only on mq-xl.....!!
							introLabel: 'butterfly3-intro',
							outroLabel: 'butterfly3-outro',
						},
						{
							el: this.$refs.FlowerTwoLeaves,
							introLabel: 'twoleaves-intro',
							outroLabel: 'twoleaves-outro',
						},
					];
					butterflyAnimations.forEach(function(butterfly, i){
						if(butterfly.el != undefined){
							// COMMENT: Trigger butterfly intro animations based on gsap introTl timeline labels
							this.introTl.addCallback(() => {
								if(scrollDirection == 'down'){
									butterfly.el.setForward();
									butterfly.el.play();
								} else {
									butterfly.el.playOutro();
								}
							}, butterfly.introLabel);

							// COMMNET: Trigger butterfly outro animations based on gsap introTl timeline labels
							this.introTl.addCallback(() => {
								if(scrollDirection == 'down'){
									butterfly.el.playOutro();
								} else {
									butterfly.el.setForward();
									butterfly.el.play();
								}
							}, butterfly.outroLabel);
						}
					}.bind(this));
				}
			},
		},
	};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import '../../assets/settings';

	.scene--butterfly-content {
		opacity: 1;
		// background-color: red;

		.bg {
			z-index: 0;
		}

		.scene-headline { // h2
			position: relative;
			
			// top: calc(18vh - rem(30));
			left: 50%;
			transform: translateX(-50%);
			text-align: center;
			max-width: 90%;

			@include mq($until: screen-xs) {
				font-size: rem(30);
				top: rem(65);
				// padding: 0 rem(30);
			}
			// @include mq($from: screen-xs) {
			// 	top: rem(100);
			// 	padding: 0 rem(30);
			// }
			@include mq($from: screen-sm){
				top: rem(90);
				font-size: rem(60);
				padding: 0 rem(30);
			}
			@include mq($until: screen-md){
				transform: translateX(-50%);
			}
			@include mq($from: screen-md){
				font-size: rem(40);
				top: rem(150);
			}
			@include mq($from: screen-xl){
				font-size: rem(60);
				top: calc(16vh - rem(30));
			}
		}

		.content {
			position: absolute;
			top: 0;
			left: 50%;
			transform: translateY(105%) translateX(-50%);
			display: flex;
			flex-direction: column;
			flex-wrap: nowrap;
			align-items: center;
			align-content: center;
			
			padding: 0;
			// flex-wrap: nowrap;
			// height: 100%;
			z-index: 2;
			box-sizing: border-box;

			@include mq($from: screen-md) {
				padding: calc(rem(80) + rem(40) + 9vh) 0 rem(30);
				box-sizing: border-box;
				top: 0;

				flex-direction: row;
				height: 100%;

				transform: translateX(-105%);
			}
		}


		.butterfly-card {
			min-width: rem(230);
			margin: rem(10) rem(15) rem(30);
			width: 69vw;
			box-shadow: rem(20) rem(20) rem(50) rgba(0, 0, 0, .1);
			cursor: pointer;
			background-color: $color-white;

			@include mq($from: screen-xs){
				width: 40vw;
			}
			@include mq($from: screen-md){
				min-width: rem(270);
				width: rem(380);
				margin: 0 rem(20);
			}

			&.video {
				background-color: transparent;
			}

			&__image-wrapper {
				width: 100%;
				overflow: hidden;
				min-height: rem(250);

				@include mq($from: screen-xs){
					min-height: rem(300);
				}

				img {
					display: block;
					width: 100%;
					will-change: transform;

					// TODO: remeber performance set to 0.5s
					// transition: transform 2.2s ease-in-out;
					// transition: transform 2.2s cubic-bezier(.65,.37,.49,.74);
					transition: transform 2.2s cubic-bezier(0.35, 0.15, 0.56, 0.79);

					@include mq($until: screen-md){
						max-height: 44vh;
						object-fit: cover;
					}
				}
			}
			&__video-wrapper {
				position: relative;
				width: 100%;
				overflow: hidden;
				min-height: rem(120);

				height: 0;
				padding-top: 84%;

				cursor: default;

				video {
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
				}
			}
			.text {
				display: block;
				color: $color-nevada;
				padding: rem(20);
				font-size: rem(15);
				@include mq($from: screen-md){
					font-size: rem(16);
				}
			}

			&:hover {
				.butterfly-card__image-wrapper {
					img {
						transform: scale(1.1);
					}
				}
			}
		}
		.butterfly1,
		.butterfly2,
		.butterfly3 {
			z-index: 2;
			position: absolute;
		}
		.butterfly1 {
			transform: rotate(20deg);
			top: 14%;
			left: 5%;

			width: rem(250);
			height: rem(250);
		}
		.butterfly2 {
			transform: rotate(25deg);
			top: 80%;
			left: 40%;

			width: rem(150);
			height: rem(150);
		}
		.butterfly3 {
			transform: rotate(-15deg);
			top: 77%;
			left: 55%;

			width: rem(160);
			height: rem(160);
		}
	}
</style>



