<template>
	<div :id="id" class="flower flower-blossom" :class="[position]">
		<lottie :id="id" :options="this.defaultOptions" v-on:animCreated="handleAnimation" v-on:animCompleted="animationCompleted" v-on:animLoaded="animationLoaded" ref="lottie" />

		<!--
		width and height not set on element, so responsive size can be acheived with css
		:height="parseInt(height)" :width="parseInt(width)"
		-->
	</div>
</template>

<script>

import LottieBase from './LottieBase';
// import blossomAnimationData from '../../../static/animation-json/test/Blossom.json';
import blossomAnimationData from '../../../static/animation-json/blossom-mask.json';

export default {
	name: 'FlowerBlossom',
	components: {
		lottie: LottieBase, // <lottie :options="defaultOptions" :height="400" :width="400" v-on:animCreated="handleAnimation"/>
	},
	props: {
		id: {
			type: String,
			default: null,
		},
		position: {
			type: String,
		},
		autoplay: Boolean,
		width: String,
		height: String,
		startAtLastFrame: {
			type: Boolean,
			default: false,
		},
	},
	data(){
		return {
			defaultOptions: {
				animationData: blossomAnimationData,
				loop: false,
				autoplay: false,
			},
			animationSpeed: 1,
			direction: 1, // forward animation,
			totalFrames: 0,
			outroStartFrame: null,
		};
	},
	mounted(){
		if(this.$props.autoplay === true){
			this.anim.play();
		}
	},
	methods: {
		handleAnimation(anim){ // callback of lottie animCreated
			
			this.anim = anim;
			this.anim.setDirection(this.direction); // forward animation to start with(might not really be necessary)
			this.totalFrames = this.anim.getDuration(true); // If true, returns duration in frames, if false, in seconds.
			if(this.$props.startAtLastFrame){
				this.jumpToFrame(this.totalFrames - 1);
			}
		},
		stop(){
			this.anim.stop();
		},
		play(){
			this.$refs.lottie.show();
			this.anim.play();
		},
		playOutro(){
			this.setReverse();
			this.anim.play();
		},
		pause(){
			this.anim.pause();
		},
		setReverse(){
			this.anim.setDirection(-1); // Reverse animation
		},
		setForward(){
			this.anim.setDirection(1); // Forward animation
		},
		changeSpeed(speed){
			this.anim.setSpeed(speed ? speed : this.animationSpeed);
		},
		animationCompleted(){
			this.$emit('animationCompleted', this.anim);
		},
		jumpToFrame(frame){
			if(frame < 0){
				frame = 0
			} else if(frame > this.totalFrames){
				frame = this.totalFrames;
			}
			this.anim.goToAndStop(frame, true); // "true" for frame input
		},
		animationLoaded(){
			this.loaded = true;
			this.$emit('animationLoaded');
		},
	},
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
@import '../../assets/settings';

// .flower-blossom {}
.flower-blossom--top-right,
.flower-blossom--top-left,
.flower-blossom--bottom-left,
.flower-blossom--bottom-right,
.flower-blossom--intro {
	position: absolute;
	z-index: 30;
}
.flower-blossom--top-left {
	top: 0;
	left: 0;
	// transform: rotate(90deg);
}
.flower-blossom--top-right {
	top: 0;
	right: 0;
	transform: rotate(90deg);
}
.flower-blossom--bottom-left {
	bottom: 0;
	left: 0;

	transform: rotate(-90deg);
}
.flower-blossom--bottom-right,
.flower-blossom--intro {
	bottom: -1%;
	right: 1%;

	// transform: scaleX(-1) rotate(-90deg);
	// transform-origin: 100% 100%;
	transform: rotate(-90deg);

	// COMMENT: as the animation is rotated -90 degrees, width becomes height, and vice versa
	width: rem(200);
	height: rem(190);

	@include mq($from: screen-xxxs){ // 360
		width: rem(250);
		height: rem(230);
	}
	@include mq($from: screen-xxs){ // 450
		width: rem(310);
		height: rem(300);
	}
	@include mq($from: screen-xs){ // 567
		width: rem(410);
		height: rem(390);
	}
	@include mq($from: screen-sm){
		width: rem(440);
		height: rem(410);
	}
	@include mq($from: screen-md){
		width: rem(489);
		height: rem(449);
	}
	// @include mq($from: screen-lg){
	// 	width: rem(539);
	// 	height: rem(609);
	// }
	@include mq($from: screen-xxl){
		width: rem(639); // as the animation is rotated -90 degrees, width becomes height, and vice versa
		height: rem(609);
	}
}
</style>



